var AppDispatcher = require('../dispatchers/app_dispatcher'),
    Store = require("../../lib/core/store");

class ConnectionStore extends Store {

  constructor() {

    this.data = {
      connection: {
        connected : true,
        reconnecting: false,
        disconnecting: false
      },
      force_hide: false
    };

    this.registerListeners();
  }

  registerListeners() {
    AppDispatcher.register({
      'updated:connection': (data) => {
        this.set('connection', data);
      },
      'unload-app': () => this.set("force_hide", true),
      'ping-response': () => {
        var connection = this.get("connection");
        if (!connection.connected) {
          connection.connected = true;
          this.set("connection", connection);
        }
      }
    });
  }

}

module.exports = new ConnectionStore();



