var Store = require("../../lib/core/store");

var dispatcher = require("../dispatchers/app_dispatcher");

class InlineDialogStore extends Store {

  constructor() {
    this.data = {
      visibleDialog: false,
      dialogData: false
    };

    this.registerListeners();
  }

  registerListeners() {
    dispatcher.register({
      'show-inline-dialog': data => {
        this.set({
          visibleDialog: data.dialog_type,
          dialogData: data.dialog_data
        });
      },
      'hide-inline-dialog': () => {
        if (this.getCurrentInlineDialog() !== false) {
          this.set({
            visibleDialog: false,
            dialogData: false
          });
        }
      }
    });
  }

  getCurrentInlineDialog() {
    return this.get("visibleDialog");
  }
}

module.exports = new InlineDialogStore();


