var Header = require("./../app_header/header");
var ConnectionNotification = require("./../connection_notification/connection_notification");
var InlineDialogManagerMixin = require("./../mixins/inline_dialog_manager_mixin");
var ModalDialogContainer = require("./../common/modal_dialog/modal_dialog_container");
var InlineDialogContainer = require("./../common/inline_dialog/inline_dialog_container");
var FlagsContainer = require("./../common/flags/flags_container");
var BodyLayout = require("./body_layout");
var FileViewer = require('./../common/file_viewer/file_viewer');
var ClientPreferencesStore = require('../../stores/client_preferences_store');
var ClientPreferencesKeys = require('../../keys/client_preferences_keys');
var AppDispatcher = require('../../dispatchers/app_dispatcher');

module.exports = React.createClass({

  displayName: "MainLayout",

  mixins: [InlineDialogManagerMixin],

  getInitialState: function () {
    return this._getClasses();
  },

  componentDidMount: function () {
    AppDispatcher.register('save-client-preferences', this._onPrefsChanged);
  },

  componentWillUnmount: function () {
    AppDispatcher.unregister('save-client-preferences', this._onPrefsChanged);
  },

  shouldComponentUpdate: function (nextProps, nextState) {
    return !_.isEqual(this.state, nextState);
  },

  _onPrefsChanged: function () {
    this.setState(this._getClasses());
  },

  _getClasses: function () {
    return {
      classes: [
        ClientPreferencesStore.getDensity(),
        ClientPreferencesStore.getTheme()
      ].join(' ')
    }
  },

  render: function () {

    return (
      <div id="page" className={this.state.classes}>
        <Header />
        <BodyLayout />
        <ConnectionNotification />
        <ModalDialogContainer />
        <InlineDialogContainer />
        <FlagsContainer />
        <FileViewer ref="fileViewer" items={this.props.files} />
      </div>
    );
  }
});


