var Link = require('./link'),
    Utils = require('../../helpers/utils'),
    EmptyState = require('./links_empty_state');

module.exports = React.createClass({

  displayName: "RightSideBarLinks",

  _getLink: function (link) {
    var time = Utils.format_time(link.date, this.props.use24hrTime);

    return <Link key={link.id} name={link.user_name} url={link.url} users={this.props.users} time={time} display_url={link.display_url}/>;
  },

  _linksBody: function () {
    var linksBody = _.map(this.props.links, function (link) {
      return this._getLink(link);
    }, this);

    return (
      <ul className="aui-nav aui-navgroup-vertical">
        {linksBody}
      </ul>
    );
  },

  render: function(){
    var cx = React.addons.classSet,
        linkClasses = cx({
          'hc-links': true,
          'hc-sidebar-scroll': true,
          'hc-bg-striped': !_.isEmpty(this.props.links)
        }),
        links_display = _.isEmpty(this.props.links) ? <EmptyState /> : this._linksBody();

    return (
      <div className={linkClasses}>
        {links_display}
      </div>
    );
  }
});



