/**
 * FileViewerActions
 */

var AppDispatcher = require('../dispatchers/app_dispatcher');

var FileViewerActions = module.exports = {

  openInFileViewer: function(data) {
    AppDispatcher.dispatch('open-in-file-viewer', data);
  }

};;


