var AppDispatcher = require('../dispatchers/app_dispatcher'),
    PreferencesStore = require("./preferences_store"),
    Store = require("../../lib/core/store"),
    Utils = require('../helpers/utils'),
    moment = require('moment');

class ChatHeaderStore extends Store {

  constructor() {

    this.default_profile = {
      email: '',
      mention_name: '',
      name: '',
      photo_large: '',
      photo_small: '',
      timezone: false,
      title: '',
      xmlns: ''
    };

    this.local = {
      profiles: {}
    };

    this.data = {
      active_chat: null,
      type: '',
      rooms: {},
      topic_editing: false,
      topic_input_value: '',
      chat: {
        jid: '',
        name: '',
        topic: '',
        photo_large: '',
        title: '',
        status: '',
        privacy: '',
        is_archived: '0',
        email: '',
        presence: {},
        time: '',
        loading_profile: true
      }
    };

    this.registerListeners();

  }

  registerListeners() {
    AppDispatcher.registerOnce({
      'hc-init': () => {
        this.set('active_chat', PreferencesStore.getChatToFocus());
      }
    });
    AppDispatcher.register({
      'updated:current_user': (user) => {
        this.local.profiles[user.user_jid] = _.assign({
          name: user.user_name,
          mention_name: user.mention
        }, user);
      },
      'updated:preferences': (prefs) => {
        this.set('active_chat', prefs.chatToFocus);
      },
      'updated:activeRooms': (rooms) => {
        this.handleRoomsUpdate(rooms);
      },
      'updated:profiles': (profiles) => {
        this.handleProfilesUpdate(profiles);
      },
      'updated:roster': (roster) => {
        this.handleRosterUpdate(roster);
      },
      'updated:active_chat': (jid) => {
        this.updateRoomHeaderState(jid);
      },
      'open-room': (data) => {
        this.updateRoomHeaderState(data.jid);
      },
      'edit-topic': () => {
        this.set('topic_editing', true);
      },
      'dismiss-topic-edit': () => {
        this.set({
          'topic_editing': false,
          'topic_input_value' : this.data.chat.topic
        });
      },
      'set-topic-input-value': (data) => {
        this.set('topic_input_value', data);
      },
      'set-topic': (data) => {
        this.data.chat.topic = this.data.rooms[data.jid].topic = data.topic;
        this.data.topic_editing = false;
        this.updateRoomHeaderState(data.jid);
      }
    });
  }

  handleRoomsUpdate(rooms) {
    var deleted = _.difference(_.keys(this.data.rooms), _.keys(rooms));
    if (deleted.length) {
      this.data.rooms = _.omit(this.data.rooms, deleted);
    } else if (this.data.active_chat &&
               this.data.active_chat != null && !this.data.active_chat.match('lobby') &&
               !this.data.topic_editing &&
               rooms[this.data.active_chat] &&
               rooms[this.data.active_chat].topic) {
      this.data.topic_input_value = rooms[this.data.active_chat].topic;
    }
    this.set('rooms', _.merge(this.data.rooms, rooms));
    this.updateRoomHeaderState(this.data.active_chat);
  }

  handleProfilesUpdate(profiles) {
    _.map(profiles, (profile, jid) => {
      this.local.profiles[jid] = profile;
      if (this.data.rooms[jid]) {
        this.data.rooms[jid] = _.merge(this.data.rooms[jid], profile);
      }
    });
    this.updateRoomHeaderState(this.data.active_chat);
  }

  updateRoomHeaderState(jid) {
    clearTimeout(this.timeout);
    if (!jid || /^lobby$/.test(jid.split('@')[0]) || !this.data.rooms[jid]) {
      return;
    }
    var type = this.data.rooms[jid].type,
        defaultPanel = {
          chat: 'files',
          groupchat: 'roster'
        };
    if (!_.isEqual(this.data.type, type)) {
      this.data.active_panel = defaultPanel[type];
    }
    if (type == 'chat' && this.data.rooms[jid]) {
      if (this.local.profiles[jid]) {
        this.data.rooms[jid].loading_profile = false;
      }
      _.merge(this.data.rooms[jid], _.clone(this.default_profile), this.local.profiles[jid]);
      if (this.data.rooms[jid].timezone) {
        var timezone_offset = parseFloat(this.data.rooms[jid].timezone.utc_offset);
        this.data.chat.time = moment().utcOffset(timezone_offset).format('ddd h:mm A');
      } else {
        this.data.chat.time = moment().format('ddd h:mm A');
      }
      this.timeout = setTimeout( () => {
        this.updateRoomHeaderState(jid);
      }, 60000);
    }
    var chat = _.assign({}, this.data.chat, this.data.rooms[jid]);
    this.set({
      active_chat: jid,
      type: type,
      chat: chat,
      active_panel: this.data.active_panel,
      topic_input_value: this.data.topic_editing ? this.data.topic_input_value : chat.topic
    });
  }

  handleRosterUpdate(roster) {
    _.map(this.data.rooms, (room) => {
      if (room) {
        if (roster[room.jid]) {
          room.presence = roster[room.jid].presence;
        }
      }
    });
    this.handleRoomsUpdate(this.data.rooms);
  }

}

module.exports = new ChatHeaderStore();



