var ModalDialog = require("../../common/modal_dialog/modal_dialog"),
    strings = require('../../../strings/dialog_strings'),
    CreateRoomForm = require("../../forms/create_room_form");

module.exports = React.createClass({

  displayName: "CreateRoomDialog",

  _dialogBody: function () {
    return (
      <CreateRoomForm loading={this.props.btnLoading}/>
    );
  },

  _dialogFooterButton: function () {
    var cx = React.addons.classSet,
        btnClasses = cx({
          'aui-button': true,
          'aui-button-primary': true
        });

    return (
      <button form="create-room-form" className={btnClasses} aria-disabled={this.props.btnLoading} disabled={this.props.btnLoading} type="submit">{strings.create_room}</button>
    );
  },

  render: function () {
    return (
      <ModalDialog dialogId="create-room-dialog"
        title={strings.create_a_new_room}
        dialogBody={this._dialogBody}
        dialogFooterButton={this._dialogFooterButton}
        btnLoading={this.props.btnLoading}
        closeLinkText="Cancel" />
    );
  }
});


