var AuiInput = require("./aui_input");

module.exports = React.createClass({

  displayName: "AuiRadio",

  _getDescription: function() {
    if (this.props.description) {
      return (
        <div className="description">{this.props.description}</div>
      )
    }
  },

  render: function() {
    var name = this.props.name || this.props.id;
    var description = this._getDescription();

    return (
      <div className="radio">
        <AuiInput type="radio" ref="radio" id={this.props.id} name={name} value={this.props.value} defaultChecked={this.props.defaultChecked} onChange={this.props.onChange} />
        <label htmlFor={this.props.id}>{this.props.label}</label>
        {description}
      </div>
    );
  }
});


