var DialogActions = require("../../../actions/dialog_actions");
var strings = require("../../../strings/dialog_strings");
var Spinner = require("../../../components/common/spinner/spinner");
var invariant = require('react/lib/invariant');

module.exports = React.createClass({

  displayName: "ModalDialog",

  componentWillMount: function() {
    invariant(_.contains(["small", "medium", "large"], this.props.size), "Size must be either small, medium or large");
  },

  componentDidMount: function() {
    document.querySelector("body").addEventListener('keydown', this._onKeyDown);
  },

  componentWillUnmount: function() {
    document.querySelector("body").removeEventListener('keydown', this._onKeyDown);
    if (document.getElementById("hc-message-input")) {
      document.getElementById("hc-message-input").focus();
    }
  },

  getDefaultProps: function() {
    return {
      closeLinkText: "Close",
      dialogId: "dialog",
      dialogBody: _.noop,
      dialogFooterButton: _.noop,
      btnLoading: false,
      size: "medium"
    }
  },

  _getCloseBtn: function () {
    return (
      <a className="aui-dialog2-header-close" onClick={this._onClick}>
        <span className="aui-icon aui-icon-small aui-iconfont-close-dialog">{strings.close}</span>
      </a>
    );
  },

  close: function() {
    DialogActions.closeDialog();
  },

  _onKeyDown: function (e) {
    var key = (window.Event) ? e.which : e.keyCode;
    if (key === 27 && !this.props.btnLoading) {
      this.close();
    }
  },

  _onClick: function (e) {
    if (!this.props.btnLoading) {
      this.close();
    }
  },

  render: function() {

    var sizeClass = "aui-dialog2-" + this.props.size,
        closeBtn = (this.props.btnLoading) ? "" : this._getCloseBtn();

    return (
      <section role="dialog" id={this.props.dialogId} className={"aui-layer aui-dialog2 " + sizeClass}>
        <header className="aui-dialog2-header">
          <h2 className="aui-dialog2-header-main">{this.props.title}</h2>
          {closeBtn}
        </header>
        <div className="aui-dialog2-content">
          {this.props.dialogBody()}
        </div>
        <footer className="aui-dialog2-footer">
          <div className="aui-dialog2-footer-actions">
            <div className="hc-dialog-btn-spinner">
              <Spinner spin={this.props.btnLoading} size="small"/>
            </div>
            {this.props.dialogFooterButton()}
            <button className="aui-button aui-button-link" aria-disabled={this.props.btnLoading} onClick={this._onClick}>{this.props.closeLinkText}</button>
          </div>
        </footer>
      </section>
    );
  }
});


