var Utils = require("../../../helpers/utils");
var FileViewerActions = require('../../../actions/file_viewer_actions');
var fileHelper = require('../../../helpers/file_helper');

module.exports = React.createClass({

  displayName: "FileMessageType",

  getInitialState: function () {
    return {
      userExpanded: null
    };
  },

  shouldComponentUpdate: function(nextProps, nextState) {
    return !_.isEqual(this.props, nextProps) || !_.isEqual(this.state, nextState);
  },

  componentDidUpdate: function() {
    this.props._setScroll();
  },

  _shouldBeExpanded: function() {
    return this.props.shouldHideGifs ? Utils.file.get_extension(this.props.msg.file_data.url) !== 'gif' : true;
  },

  _isExpanded: function() {
    return (this.state.userExpanded === null) ? this._shouldBeExpanded() : this.state.userExpanded;
  },

  render: function () {
    var description = <span className="description" dangerouslySetInnerHTML={{__html: this.props.msg.rendered_body}}></span>;
    var cx = React.addons.classSet,
        fileIcons = cx({
          'aui-icon': true,
          'hipchat-icon-small': true,
          'hc-file-icon': true
        }),
        fileName = decodeURIComponent(this.props.msg.file_data.url.split('/').pop());

    fileIcons = fileIcons + " " + Utils.file.get_icon_class(fileName);

    switch (this.props.msg.file_data.filetype) {
      case 'img':
        return <div data-mid={this.props.msg.mid} className="msg-line">
          <div className="toggle-wrap" onClick={this._toggleImage}>
            <span className="toggle-image collapse"></span>
          </div>
          <div className="file-meta">
            <span className={fileIcons}></span>
            <a href={this.props.msg.file_data.url} target="_blank">{fileName}</a>
            <span className="file-size">{this.props.msg.file_data.file_size}</span>
          </div>
          {
            this._isExpanded() ?
            <div className="file-preview-wrapper">
              <a href={this.props.msg.file_data.url} target="_blank" title={fileHelper.basename(this.props.msg.file_data.name)} onClick={this._onClick}>
                <img src={this.props.msg.file_data.thumb_url} />
              </a>
            </div>
            :
            <div className="file-preview-wrapper info">
              <i>Image hidden</i>
            </div>
          }
          {description}
        </div>;
        break;
      case 'video':
        return <div data-mid={this.props.msg.mid} className="msg-line">
          <div className="file-meta">
            <span className={fileIcons}></span>
            <a href={this.props.msg.file_data.url} target="_blank">{fileName}</a>
            <span className="file-size">{this.props.msg.file_data.file_size}</span>
          </div>
          <div className="file-preview-wrapper video-wrap">
              <span className="file-video-default-poster" onClick={this._onClick}></span>
          </div>
          {description}
        </div>;
        break;
      default:
        return <div data-mid={this.props.msg.mid} className="msg-line">
          <div className="file-meta">
            <span className={fileIcons}></span>
            <a href={this.props.msg.file_data.url} target="_blank">{fileName}</a>
            <span className="file-size">{this.props.msg.file_data.file_size}</span>
          </div>
          {description}
        </div>;
        break;
    }
  },

  _toggleImage: function (e) {
    this.setState({
      userExpanded: !this._isExpanded()
    });
    $(e.currentTarget).find('span').toggleClass('expand collapse');
  },

  _videoTypeSupported: function(ext) {
    var video = document.createElement( "video" );
    if (video.canPlayType) {
      return {
        mp4: (video.canPlayType('video/mp4; codecs="mp4v.20.8"') || video.canPlayType('video/mp4; codecs="avc1.42E01E"') || video.canPlayType('video/mp4; codecs="avc1.42E01E, mp4a.40.2"')),
        m4v: (video.canPlayType('video/mp4; codecs="mp4v.20.8"') || video.canPlayType('video/mp4; codecs="avc1.42E01E"') || video.canPlayType('video/mp4; codecs="avc1.42E01E, mp4a.40.2"')),
        ogv: video.canPlayType('video/ogg; codecs="theora"'),
        webm: video.canPlayType('video/webm; codecs="vp8, vorbis"')
      }[ext];
    }
    return false;
  },

  _onClick: function(evt){
    if (fileHelper.shouldOpenFileViewer(evt)) {
      evt.preventDefault();
      FileViewerActions.openInFileViewer(this.props.msg.file_data);
    }
  }

});



