var ModalDialog = require("../../common/modal_dialog/modal_dialog"),
  RoomsNavStore = require("../../../stores/rooms_nav_store"),
  AppDispatcher = require("../../../dispatchers/app_dispatcher"),
  DialogActions = require("../../../actions/dialog_actions"),
  FlagActions = require("../../../actions/flag_actions"),
  strings = require('../../../strings/dialog_strings'),
  RoomDropDownActions = require("../../../actions/room_dropdown_actions"),
  Utils = require("../../../helpers/utils");

module.exports = React.createClass({

  displayName: "ArchiveRoomDialog",

  componentDidMount: function () {
    RoomsNavStore.on(['change'], this._onChange);
  },

  componentWillUnmount: function () {
    RoomsNavStore.off(['change'], this._onChange);
  },

  getInitialState: function () {
    return this._getActiveChatState();
  },

  _getActiveChatState: function () {
    return {
      active_chat: RoomsNavStore.get("active_chat"),
      rooms: RoomsNavStore.get("rooms")
    };
  },

  _onChange: function () {
    this.setState(this._getActiveChatState());
  },

  _dialogBody: function () {
    return (
      this.props.archive ?
        <div>
        <p><strong>{strings.really_archive(this._getRoomName())}</strong></p>
        <p>{strings.confirm_archive}</p>
      </div> : <p>{strings.really_unarchive(this._getRoomName())}</p>
    );
  },

  _dialogFooterButton: function () {
    return (
      <button onClick={this.props.archive ? this._archiveRoom : this._unarchiveRoom} className="aui-button aui-button-primary">{this.props.archive ? strings.archive : strings.unarchive}</button>
    );
  },

  _getRoomName: function () {
    return Utils.room.get_room_name(_.indexBy(this.state.rooms.rooms, "jid"), this.state.active_chat);
  },

  _archiveRoom: function (e) {
    e.preventDefault();
    RoomDropDownActions.archiveRoom({
      jid: this.state.active_chat
    }, this._handleResponse);
    DialogActions.closeDialog();
  },

  _unarchiveRoom: function (e) {
    e.preventDefault();
    RoomDropDownActions.unarchiveRoom({
      jid: this.state.active_chat
    }, this._handleResponse);
    DialogActions.closeDialog();
  },

  _handleResponse: function (data) {
    if (data.error) {
      var error_msg = this.props.archive ? strings.archive_fail : strings.unarchive_fail;
      this._throwFlagError(error_msg);
    } else {
      var success_msg = this.props.archive ? strings.room_archived : strings.room_unarchived;
      this._throwFlagSuccess(success_msg);
    }
  },

  _throwFlagSuccess: function (msg) {
    FlagActions.showFlag({
      type: "success",
      body: msg,
      close: "auto"
    });
  },

  _throwFlagError: function (error) {
    FlagActions.showFlag({
      type: "error",
      body: this._flagBody(error),
      close: "manual"
    });
  },

  _flagBody: function (error_msg) {
    return (
      <div>
        <p className="hc-message-body">{error_msg}</p>
        <ul className="aui-nav-actions-list">
          <li><a onClick={this.props.archive ? this._archiveRoom : this._unarchiveRoom} href="#">{strings.try_again}</a></li>
        </ul>
      </div>
    );
  },

  render: function () {
    return (
      <ModalDialog dialogId="archive-room-dialog"
        title={this.props.archive ? strings.archive_room_name(this._getRoomName()) : strings.unarchive_room_name(this._getRoomName())}
        dialogBody={this._dialogBody}
        dialogFooterButton={this._dialogFooterButton}
        closeLinkText="Cancel" />
    );
  }
});


