var Promise = require('es6-promise').Promise;
var AuiSelect2FieldGroup = require("../common/aui/form/aui_select2_fieldgroup"),
    AuiTextAreaFieldGroup = require("../common/aui/form/aui_textarea_fieldgroup"),
    FlagActions = require("../../actions/flag_actions"),
    FormActions = require("../../actions/form_actions"),
    DialogActions = require("../../actions/dialog_actions"),
    AppDispatcher = require('../../dispatchers/app_dispatcher'),
    FormsStore = require("../../stores/forms_store"),
    strings = require('../../strings/forms_strings'),
    FormErrors = require("../../strings/form_error_strings"),
    Utils = require("../../helpers/utils");

module.exports = React.createClass({

  displayName: "InviteUsersForm",

  componentDidMount: function() {
    FormsStore.on('change', this._onChange);
  },

  componentWillUnmount: function() {
    FormsStore.off('change', this._onChange);
  },

  getInitialState: function () {
    return this._getState();
  },

  _onChange: function(){
    this.setState(this._getState());
  },

  _getState: function(){
    return {
      roster: FormsStore.get("roster"),
      currentUser: FormsStore.get("currentUser"),
      activeChatParticipants: FormsStore.get("activeChatParticipants"),
      errors: {}
    };
  },

  _rosterForSelect: function () {
    return Utils.roster.format_for_select2(_.union(this.state.currentUser.user_jid, this.state.activeChatParticipants), this.state.roster);
  },

  _onSubmit: function (e) {
    e.preventDefault();
    var form_inputs = $(this.refs.form.getDOMNode()).serializeArray(),
        users = form_inputs[0].value,
        msg = form_inputs[1].value,
        promises = [],
        errors;

    errors = this._validate(users, msg);
    if (errors) {
      this.setState({
        errors: errors
      });
    } else {
      promises.push(this._inviteUsers(users.split(','), msg));
      this._handlePromises(promises);
      this._closeDialog();
    }
  },

  _validate: function (users, msg) {
    if (!users) {
      return {
        user: FormErrors.invite_users_form.no_users
      };
    } else if (msg && msg.length > 250) {
      return {
        msg: FormErrors.invite_users_form.msg_too_long
      }
    }
  },

  _inviteUsers: function (user_jids, msg) {
    return new Promise( (resolve, reject) => {
      FormActions.inviteUsers({
        room_jid: this.props.room_jid,
        user_jids: user_jids,
        reason: msg
      });
      resolve();
    });
  },

  _handlePromises: function (promises) {
    Promise.all(promises).then(function(data) {
      this._throwFlagSuccess();
    }.bind(this), function(error) {
      this._throwFlagError();
    }.bind(this));
  },

  _throwFlagSuccess: function () {
    FlagActions.showFlag({
      type: "success",
      body: strings.success.users_invited,
      close: "auto"
    });
  },

  _throwFlagError: function () {
    FlagActions.showFlag({
      type: "error",
      body: this._flagErrorActions(strings.fail.invite_fail),
      close: "manual"
    });
  },

  _flagErrorActions: function (error_msg) {
    return (
      <div>
        <p className="hc-message-body">{error_msg}</p>
        <ul className="aui-nav-actions-list">
          <li><a onClick={this._flagActionClick} href="#">strings.button.try_again</a></li>
        </ul>
      </div>
    );
  },

  _flagActionClick: function (e) {
    e.preventDefault();
    var flag_index = $(e.target).closest(".flag").data('flag-index');
    FlagActions.removeFlag(flag_index);
    this._showDialog();
  },

  _showDialog: function () {
    DialogActions.showInviteUsersDialog();
  },

  _closeDialog: function () {
    DialogActions.closeDialog();
  },

  render: function () {
    var errors = this.state.errors;
    return (
      <form id="invite-users-form" ref="form" className="aui" onSubmit={this._onSubmit}>
        <AuiSelect2FieldGroup id="invite-users-people" label={strings.label.these_people} multiple={true} data={this._rosterForSelect()} error={errors.user} ref="user-select"/>
        <AuiTextAreaFieldGroup id="invite-users-message" label={strings.label.message} rows="5" error={errors.msg} ref="invite_message"/>
      </form>
    );
  }
});


