var Store = require("../../lib/core/store");

var dispatcher = require("../dispatchers/app_dispatcher");

class ModalDialogStore extends Store {

  constructor() {
    this.data = {
      visibleDialog: false,
      dialogData: false,
      btnLoading: false
    };

    this.registerListeners();
  }

  registerListeners() {
    dispatcher.register({
      'show-modal-dialog': (data) => {
        this.set({
          visibleDialog: data.dialog_type,
          dialogData: data.dialog_data,
          btnLoading: false
        });
      },
      'hide-modal-dialog': () => {
        this.set({
          visibleDialog: false,
          dialogData: false,
          btnLoading: false
        });
      },
      'modal-dialog-btn-loading': (data) => {
        this.set({
          btnLoading: data.loading
        });
      }
    });
  }

  getCurrentModalDialog() {
    return this.get("visibleDialog");
  }
}

module.exports = new ModalDialogStore();


