var ModalDialog = require("../../common/modal_dialog/modal_dialog"),
    RenameRoomForm = require("../../forms/rename_room_form"),
    strings = require('../../../strings/dialog_strings'),
    Utils = require("../../../helpers/utils");

module.exports = React.createClass({

  displayName: "RenameRoomDialog",

  _dialogBody: function () {
    return (
      <RenameRoomForm jid={this.props.jid} name={this.props.name} loading={this.props.btnLoading}/>
    );
  },

  _dialogFooterButton: function () {
    var cx = React.addons.classSet,
        btnClasses = cx({
          'aui-button': true,
          'aui-button-primary': true
        });

    return (
      <button form="rename-room-form" className={btnClasses} aria-disabled={this.props.btnLoading} disabled={this.props.btnLoading} type="submit">{strings.rename}</button>
    );
  },

  render: function () {
    return (
      <ModalDialog dialogId="rename-room-dialog"
        title={strings.rename_room(this.props.name)}
        dialogBody={this._dialogBody}
        dialogFooterButton={this._dialogFooterButton}
        btnLoading={this.props.btnLoading}
        closeLinkText={strings.cancel} />
    );
  }
});


