module.exports = React.createClass({

  displayName: "ChatWindowEmptyState",

  render: function(){

    return (

      <div className="empty">
        {
          /@chat/.test(this.props.chat.jid) ?
            <div className="empty-state chat">
              <div className="empty-state-img"></div>
              <div className="empty-state-msg">
                <p className="empty-header">
                  Say Hello to {this.props.chat.name.split(' ')[0]}!
                </p>
                <p className="empty-msg">
                  You haven't talked to {this.props.chat.name.split(' ')[0]} before. Why not break the ice with
                  <br/>
                  an emoticon? (fonzie) <img src="wc/assets/img/embedded/fonzie@3x.png" width="34" height="25"/>
                </p>
              </div>
            </div>
            :
            <div className="empty-state groupchat">
              <div className="empty-state-img"></div>
              <div className="empty-state-msg">
                <p className="empty-header">
                  Welcome to the {this.props.chat.name} room!
                </p>
                <p className="empty-msg">
                  This window is kinda empty, huh? It fills up fast when you
                  <br/>
                  start chatting with your friends. Happy chatting!
                </p>
              </div>
            </div>
          }
      </div>

    );
  }

});



