var FlagsStore = require("../../../stores/flags_store"),
    Flag = require("../flags/flag");

module.exports = React.createClass({

  displayName: "FlagsContainer",

  componentDidMount: function() {
    FlagsStore.on('change', this._onChange);
  },

  componentWillUnmount: function() {
    FlagsStore.off('change', this._onChange);
  },

  getInitialState: function() {
    return FlagsStore.getAll();
  },

  _onChange: function() {
    this.setState(FlagsStore.getAll());
  },

  render: function() {
    return (
      <div className="hc-flags-container">
        {
          _.map(this.state.flags, function (flag, index) {
            return <Flag flag_index={index} title={flag.title} type={flag.type} body={flag.body} close={flag.close}/>;
          }, this)
        }
      </div>
    );
  }
});


