
class HCApiClient {

  constructor(token, api_root) {
    this.token = token;
    this.api_root = 'https://' + (api_root || 'api.hipchat.com') + '/v2/';

    this.room = {

      recent_history: (data, callback) => {
        this.request('GET', 'room/' + data.path.identifier + '/history/latest', data.params, callback);
      },

      invite_users: (data, callback) => {
        this.request('POST', 'room/' + data.path.identifier + '/invite/' + data.user_id, data.params, callback);
      },

      update_room: (data, callback) => {
        this.request('PUT', 'room/' + data.path.identifier, data.params, callback, true);
      }

    };

    this.user = {

      recent_history: (data, callback) => {
        this.request('GET', 'user/' + data.path.identifier + '/history/latest', data.params, callback);
      }

    };
  }

  url(path, query) {
    var url = this.api_root + escape(path),
        query = _.omit(query, (val) => {return !val;}),
        queryString = function (query) {
          var query_string = !_.isEmpty(query) ? '?' : '';
          for (var key in query) {
            if (query[key]) query_string += (key + '=' + query[key]);
            query_string += '&';
          }
          return query_string;
        };
    if (!query) {
      return url;
    } else if (typeof query === 'object') {
      return url + queryString(query);
    }
  }

  getUrl(path, payload, postJSON) {
    if (postJSON) {
      return this.url(path);
    } else {
      return this.url(path, payload);
    }
  }

  request(method, path, payload, callback, postJSON) {
    var xhr = new XMLHttpRequest(),
        token = this.token,
        handleSend = this.handleSend,
        handleResponse = this.handleResponse,
        url = this.getUrl(path, payload, postJSON);

    xhr.onreadystatechange = function () {
      if (xhr.readyState == 1) {
        handleSend(xhr, token, payload, postJSON);
      }
      if (xhr.readyState == 4) {
        handleResponse(xhr.response, callback);
      }
    };

    if ("withCredentials" in xhr) {
      xhr.open(method, url, true);
    } else if (typeof XDomainRequest != "undefined") {
      xhr = new XDomainRequest();
      xhr.open(method, url);
    }
  }

  handleSend(xhr, token, payload, postJSON) {
    xhr.setRequestHeader('Authorization', 'Bearer ' + token);

    if (postJSON) {
      xhr.setRequestHeader("Content-Type", "application/json");
      xhr.send(JSON.stringify(payload));
    } else {
      xhr.send();
    }
  }

  handleResponse(response, callback) {
    if (response !== "") {
      response = JSON.parse(response);
    }
    callback(response);
  }

}

module.exports = HCApiClient;


