var PresenceIcon = require("../common/icon/presence-icon"),
    PureRenderMixin = React.addons.PureRenderMixin;

module.exports = React.createClass({

  displayName: "RoomsNavItemPerson",

  mixins: [PureRenderMixin],

  render: function() {
    return (
      <a className="aui-nav-item" title={this.props.status} onClick={this.props.switchTab}>
        <PresenceIcon active={true} presence={this.props.presence} mobile={this.props.mobile} />
        <span className="room-name">{this.props.name}</span>
        {this.props.unreadBadge}
      </a>
    );
  }
});


