var Promise = require('es6-promise').Promise,
    AppDispatcher = require('../../dispatchers/app_dispatcher'),
    RoomPrivacyRadios = require("../../components/forms/room_privacy_radios"),
    FlagActions = require("../../actions/flag_actions"),
    DialogActions = require("../../actions/dialog_actions"),
    FormActions = require("../../actions/form_actions"),
    strings = require('../../strings/forms_strings'),
    Utils = require("../../helpers/utils");


module.exports = React.createClass({

  displayName: "RoomPrivacyForm",

  shouldComponentUpdate: function (nextProps) {
    if (!nextProps.loading) {
      return true;
    } else {
      return false;
    }
  },

  _onSubmit: function (e) {
    e.preventDefault();
    var form_inputs = $(this.refs.form.getDOMNode()).serializeArray(),
        submit_data = {
          jid: this.props.jid,
          privacy: form_inputs[0].value
        };

    if (submit_data.privacy !== this.props.privacy) {
      DialogActions.startBtnLoading();
      FormActions.changeRoomPrivacy(submit_data, this._handleResponse);
    }
  },

  _handleResponse: function (data) {
    if (data.error) {
      var error_msg = (Utils.dot(data, "error.text.__text")) ? data.error.text.__text : strings.fail.change_privacy_flag;
      this._throwFlagError(error_msg);
    } else {
      var privacy = (this.props.privacy === "public") ? "private" : "public";
      var success_msg = strings.success.privacy_changed(this.props.name, privacy);
      this._throwFlagSuccess(success_msg);
    }
    this._closeDialog();
  },

  _throwFlagSuccess: function (msg) {
    FlagActions.showFlag({
      type: "success",
      body: msg,
      close: "auto"
    });
  },

  _throwFlagError: function (error) {
    FlagActions.showFlag({
      type: "error",
      body: this._flagBody(error),
      close: "manual"
    });
  },

  _flagActionClick: function (e) {
    e.preventDefault();
    var flag_index = $(e.target).closest(".flag").data('flag-index');
    FlagActions.removeFlag(flag_index);
    this._showDialog();
  },

  _flagBody: function (error_msg) {
    return (
      <div>
        <p className="hc-message-body">{error_msg}</p>
        <ul className="aui-nav-actions-list">
          <li><a onClick={this._flagActionClick} href="#">{strings.button.try_again}</a></li>
        </ul>
      </div>
    );
  },

  _showDialog: function () {
    DialogActions.showRoomPrivacyDialog({
      jid: this.props.jid,
      name: this.props.name,
      privacy: this.props.privacy
    });
  },

  _closeDialog: function () {
    DialogActions.closeDialog();
  },

  render: function () {
    return (
      <form id="room-privacy-form" ref="form" className="aui" onSubmit={this._onSubmit}>
        <RoomPrivacyRadios defaultChecked={this.props.privacy} private_desc={strings.description.privacy}/>
      </form>
    );
  }
});


