var Store = require("../../lib/core/store");
var AppDispatcher = require("../dispatchers/app_dispatcher");
var Utils = require("../helpers/utils");
var Strings = require('../strings/file_input_errors');
var Promise = require('es6-promise').Promise;

class ChatInputStore extends Store {

  constructor() {

    this.data = this.getDefaults();

    this.local = {
      // map of jid to unsent text values (used when user types
      unsentText: {}
    };

    this.registerListeners();

    this.emoticonsDeferred = Utils.promise.defer();
    this.smileysDeferred = Utils.promise.defer();

    this.emoticonsDeferred.promise.then((data) => {
      this.data.path_prefix = data.path_prefix;
      this.data.emoticons = data.icons;
    });
    this.smileysDeferred.promise.then((data) => {
      var unique = _.indexBy(_.indexBy(data, 'file'), 'shortcut');
      this.data.smileys = unique;
    });

    this.handlePromises();

  }

  getDefaults(){
    return {
      text: '',
      active_chat: null,
      attachment_expanded: false,
      file_name: '',
      file: null,
      uploading: false,
      chat_type: '',
      message_id: 0,
      path_prefix: '',
      smileys: {},
      file_error: false,
      file_error_message: '',
      user_state: 'active',
      user_is_admin: false,
      mention_text: '',
      mention_selected_item: 0,
      mention_results_count: 0,
      emoticon_text: '',
      emoticon_selected_item: 0,
      emoticon_results_count: 0
    };
  }

  registerListeners() {
    AppDispatcher.registerOnce({
      'chat-fully-initialized': () => {
        this.preloadIcons();
      }
    });
    AppDispatcher.register({
      'updated:current_user': (data) => {
        this.data.user_is_admin = data.is_admin;
      },
      'open-room': (data) => {
        this.set({
          active_chat : data.jid,
          chat_type : data.type
        });
      },
      'close-room': (data) => {
        this.deleteUnsentText(data.jid);
      },
      'updated:activeRooms': (rooms) => {
        if (rooms[this.data.active_chat]) {
          this.set('chat_type', rooms[this.data.active_chat].type);
        }
      },
      'send-message': () => {
        this.data.message_id++;
        this.set({
          text : '',
          message_id : this.data.message_id,
          user_state: 'active'
        });
      },
      'updated:smileys': (data) => {
        this.smileysDeferred.resolve(data);
      },
      'updated:emoticons': (data) => {
        this.emoticonsDeferred.resolve(data);
      },
      'initial-select-room': (jid) => {
        this.set({
          active_chat : jid
        });
      },
      'before:updated:active_chat': (jid) => {
        this.setUnsentText(this.data.active_chat);
      },
      'updated:active_chat': (jid) => {
        this.set({
          active_chat : jid,
          chat_type : Utils.room.detect_chat_type(jid),
          text: this.getUnsentText(jid)
        });
      },
      'upload-file': () => {
        this.set('uploading', true);
      },
      'upload-successful': (data) => {
        this.set({
          uploading: false,
          attachment_expanded : false,
          file : null,
          file_name : '',
          text: ''
        });
        this.handleUploadSuccess(data.responseXML);
      },
      'upload-failed': () => {
        this.set({
          uploading: false
        });
      },
      'set-message-value': (text) => {
        this.set('text', text);
      },
      'expand-attachment': (data) => {
        this.set({
          attachment_expanded : true,
          file : data.file,
          file_name : data.file_name
        });
      },
      'close-attachment': () => {
        this.set({
          attachment_expanded : false,
          file : null,
          file_name : '',
          file_error: false
        });
      },
      'sender-clicked': (data) => {
        if (this.data.text.indexOf(data.mention + ' ') == -1){
          this.set({
            text : this.data.text + data.mention + ' '
          });
        }
      },
      'change-filename': (data) => {
        this.set('file_name', data.file_name);
      },
      'smiley-chosen': (data) => {
        this.set({
            text : this.data.text + data.shortcut + ' '
          });
      },
      'set-user-state': (data) => {
        if (this.data.user_state != data.state) {
          this.data.user_state = data.state;
        }
      },
      'file-error': (message_key) => {
        this.set({
          'file_error' : true,
          'file_error_message': Strings[message_key]
        });
      },
      'clear-errors': () => {
        this.set({
          'file_error': false,
          'file_error_message': ''
        });
      }
    });
  }

  setUnsentText(jid) {
    // testing for undefined and empty string before setting value
    // NOTE:  We should probably have a constant for our lobby jid construct
    if(jid && jid.length > 0 && jid !== "lobby") {
      this.local.unsentText[jid] = this.data.text;
    }
  }

  getUnsentText(jid) {
    return this.local.unsentText[jid] || '';
  }

  deleteUnsentText(jid) {
    delete this.local.unsentText[jid];
  }

  handlePromises() {
    Promise.all([this.emoticonsDeferred.promise, this.smileysDeferred.promise]).then(() => {
      this.set({
        smileys: this.data.smileys,
        emoticons: this.data.emoticons
      });
    });
  }

  handleUploadSuccess(xml) {
    AppDispatcher.dispatch('send-upload-message', {
      type: this.data.chat_type,
      jid: this.data.active_chat,
      file_info: xml
    })
  }

  preloadIcons() {
    _.forEach(['smileys', 'emoticons'], (icons) => {
      _.forEach(this.data[icons], (icon) => {
        var $img = $('<img />').attr('src',this.data.path_prefix + '/' + icon.file);
        var imgTimeout = _.delay(()=>{
          $img.off('load');
        }, 5000);
        $img.on('load', ()=>{
          clearTimeout(imgTimeout);
        });
      });
    });
  }

}

module.exports = new ChatInputStore();



