var RoomsNavItemPerson = require('./rooms_nav_item_person');
var RoomsNavItemRoom = require('./rooms_nav_item_room');
var UnreadBadge = require('./unread_badge');

module.exports = React.createClass({

  displayName: "RoomsNavItem",

  render: function() {
    var unreadBadge = this.props.room.unreadCount > 0 && !this.props.active ?
      <UnreadBadge unreadCount={this.props.room.unreadCount} hasMention={this.props.room.hasMention} /> : '';

    var roomLink = /@conf/.test(this.props.room.jid) ?
      <RoomsNavItemRoom   name={this.props.room.name}
                          privacy={this.props.room.privacy}
                          switchTab={this.props.switchTab}
                          active={this.props.active}
                          unreadBadge={unreadBadge} /> :
      <RoomsNavItemPerson presence={this.props.room.presence.show}
                          status={this.props.room.presence.status}
                          mobile={this.props.room.mobile}
                          switchTab={this.props.switchTab}
                          active={this.props.active}
                          name={this.props.room.name}
                          unreadBadge={unreadBadge} />;

    var cx = React.addons.classSet;
    var classes = cx({
      'hc-tab': true,
      'aui-nav-selected': this.props.active,
      'hc-has-badge': (this.props.room.unreadCount > 0),
      'hc-room': /@conf/.test(this.props.room.jid),
      'hc-person': !/@conf/.test(this.props.room.jid)
    });
    return (
      <li draggable="true" className={classes} onDragStart={this.props.dragStart} onDragEnd={this.props.dragEnd} onClick={this._onClick} onMouseEnter={this._onMouseEnter} onMouseLeave={this._onMouseLeave} data-jid={this.props.jid}>
        {roomLink}
        <a className="hc-tab-close" onClick={this.props.closeTab}>
          <span className="aui-icon hipchat-icon-xsmall hc-close-icon icon-close"/>
        </a>
      </li>
    );
  }
});


