module.exports = React.createClass({

  displayName: "AuiFormFieldSet",

  render: function() {
    return (
      <fieldset className="group">
        <legend><label htmlFor={this.props.id}>{this.props.label}</label></legend>
        {this.props.children}
      </fieldset>
    );
  }

});


