var RoomNavStore = require('../../stores/rooms_nav_store'),
    Utils = require('../../helpers/utils'),
    SidebarStrings = require('../../strings/sidebar_strings');

function isBelow(el, boundary) {
  return el.offsetTop > boundary;
}

function isAbove(el, boundary) {
  return (el.offsetTop + el.offsetHeight) < boundary;
}

module.exports = React.createClass({

  displayName: "UnreadScroller",

  getInitialState: function(){
    return {
      hideTop: true,
      hideBottom: true
    };
  },

  componentWillMount: function(){
    this._debouncedOnChange = _.throttle(this._onChange, 100, {trailing:true, leading:true});
    this._tabsAbove = [];
    this._tabsBelow = [];
    RoomNavStore.on('change', this._debouncedOnChange);
  },

  componentWillUnmount: function(){
    RoomNavStore.off('change', this._debouncedOnChange);
  },

  render: function() {
    var cx = React.addons.classSet;
    var topClass = cx({
      'hidden': this.state.hideTop
    });
    var bottomClass = cx({
      'hidden': this.state.hideBottom
    });
    return (
      <nav className="aui-navgroup aui-navgroup-vertical hc-sidebar hc-sidebar-scroll" ref="target" onScroll={this._debouncedOnChange}>
        <div className={'hc-unread-scroller hc-unread-scroller-top ' + topClass}>
          <a href="#" onClick={this._scrollUpToUnread}><span className="aui-icon aui-icon-small aui-iconfont-arrows-up">{SidebarStrings.scroll_up_to_unread}</span></a>
        </div>

        {this.props.children}

        <div className={'hc-unread-scroller hc-unread-scroller-bottom ' + bottomClass}>
          <a href="#" onClick={this._scrollDownToUnread}><span className="aui-icon aui-icon-small aui-iconfont-arrows-down">{SidebarStrings.scroll_down_to_unread}</span></a>
        </div>
      </nav>
    );
  },

  _onChange: function(){
    this.setState(this._getState());
  },

  _getState: function(){
    var node = this.refs.target.getDOMNode();
    var tabs = $('.hc-badge').closest('.hc-tab');
    this._tabsAbove = tabs.filter((idx,el) => {
      return isAbove(el, node.scrollTop);
    });
    this._tabsBelow = tabs.filter((idx,el) => {
      return isBelow(el, node.scrollTop + node.offsetHeight);
    });
    return {
      hideTop: !this._tabsAbove.length,
      hideBottom: !this._tabsBelow.length,
      topBoundary: node.scrollTop,
      bottomBoundary: node.scrollTop + node.offsetHeight
    };
  },

  _scrollUpToUnread: function(evt){
    evt.preventDefault();
    this.setState(this._getState());
    if (this._tabsAbove.length > 0) {
      Utils.scrollIntoViewIfNeeded(this._tabsAbove.get(this._tabsAbove.length-1), this.refs.target.getDOMNode());
    }
  },

  _scrollDownToUnread: function(evt){
    evt.preventDefault();
    this.setState(this._getState());
    if (this._tabsBelow.length > 0) {
      Utils.scrollIntoViewIfNeeded(this._tabsBelow.get(0), this.refs.target.getDOMNode());
    }
  }
});


