var InlineDialogStore = require("../../../stores/inline_dialog_store");
var InviteUsersInlineDialog = require("../../invite_users/invite_users_inline_dialog");
var UserStatusMessageForm = require("../../app_header/user_status_message_form");

module.exports = React.createClass({

  displayName: "InlineDialogContainer",

  componentDidMount: function() {
    InlineDialogStore.on('change', this._onChange);
  },

  componentWillUnmount: function() {
    InlineDialogStore.off('change', this._onChange);
  },

  getInitialState: function() {
    return InlineDialogStore.getAll();
  },

  _onChange: function() {
    this.setState(InlineDialogStore.getAll());
  },

  _getDialog: function() {
    var currentDialogId = InlineDialogStore.getCurrentInlineDialog();
    var currentDialog = false;

    if (currentDialogId) {
      switch (currentDialogId) {
        case "invite-users-inline-dialog":
          currentDialog = <InviteUsersInlineDialog {...this.state.dialogData} />;
          break;

        case "user-status-message-inline-dialog":
          currentDialog = <UserStatusMessageForm {...this.state.dialogData} />;
          break;
      }
    }

    return currentDialog;
  },

  render: function() {

    var dialog = this._getDialog();

    return (
      <div>
        {dialog}
      </div>
    );
  }
});


