var ConnectionStore = require('../../stores/connection_store'),
    ConnectionActions = require('../../actions/connection_actions'),
    strings = require('../../strings/connection_notification_strings'),
    Spinner = require('../common/spinner/spinner');

var getConnectionState = function () {
  return ConnectionStore.getAll();
};

module.exports = React.createClass({

  displayName: "ConnectionNotification",

  getInitialState: function () {
    return getConnectionState();
  },

  componentDidMount: function () {
    ConnectionStore.on('change', this._onChange);
  },

  componentWillUnmount: function () {
    ConnectionStore.off('change', this._onChange);
  },

  render: function () {
    return (
      <div className={"hc-box-shadow hc-message warning hc-connection-notification " + (this._shouldHideNotification() ? 'transparent ' : ' ')+ (this.state.connection.reconnecting ? 'waiting' : '')}>
        <div className="spin-wrap">
          <Spinner spin={this.state.connection.reconnecting} className="reconnect-spinner" size="small"/>
        </div>
        <p className="title">
          <strong>{strings.failed_connection}</strong>
        </p>
        <p>
          <a href="javascript:void(0)" onClick={this._onClick} className={(this.state.connection.reconnecting ? 'inactive' : '')}>{this.state.connection.reconnecting ? strings.attempt_reconnect : strings.reconnect_now}</a>
        </p>
      </div>
    );
  },

  _onChange: function () {
    this.setState(getConnectionState());
  },

  _onClick: function (e) {
    if (!$(e.currentTarget).hasClass('inactive')){
      ConnectionActions.handleReconnectLinkClick();
    }
  },

  _shouldHideNotification: function () {
    return this.state.force_hide || (this.state.connection.connected && this.state.connection.disconnecting) || this.state.connection.connected
  }

});


