var File = require('./file'),
    Utils = require('../../helpers/utils'),
    EmptyState = require('./files_empty_state'),
    RightPanelActions = require('../../actions/right_panel_actions');

module.exports = React.createClass({

  displayName: "RightSideBarFiles",

  componentDidMount: function() {
    var size = 0;
    if (this.props.files){
        size = this.props.files.length;
    }
    RightPanelActions.filesMounted({id: _.uniqueId(), size: size});
  },

  _getFile: function (file) {
    var time = Utils.format_time(file.date, this.props.use24hrTime);

    return <File key={file.id} file={file} time={time}/>;
  },

  _filesBody: function () {
    var filesBody = _.map(this.props.files, function (file) {
      return this._getFile(file);
    }, this);

    return (
      <ul className="aui-nav aui-navgroup-vertical">
        {filesBody}
      </ul>
    );
  },

  render: function(){
    var cx = React.addons.classSet,
        fileClasses = cx({
          'hc-files': true,
          'hc-sidebar-scroll': true
        }),
        files_display = _.isEmpty(this.props.files) ? <EmptyState /> : this._filesBody();

    return (
      <div className={fileClasses}>
        {files_display}
      </div>
    );
  }
});



