var AppDispatcher = require('../dispatchers/app_dispatcher');
var Store = require("../../lib/core/store");
var Utils = require('../helpers/utils');

class LobbyStore extends Store {

  constructor() {

    this.data = {
      all: {},
      filtered: {},
      filter: {
        scope: "all",
        query: ""
      }
    };

    this.registerListeners();

  }

  registerListeners() {
    AppDispatcher.register({
      'updated:roster': (roster) => {
        this.handleRosterUpdate(roster);
      },
      'updated:allRooms': (rooms) => {
        this.handleRoomsUpdate(rooms);
      },
      'room-deleted': (room) => {
        this.handleRoomDeleted(room.jid);
      },
      'filter-lobby': (filter) => {
        this.handleFilter(filter);
      }
    });
  }

  assignAndSort(items) {
    return _.indexBy(
      _.sortBy(
        _.reject(
          _.assign(this.data.all, items)
          , function (item) {
            return !item || item.name === '';
          }
        ), 'name'), 'jid');
  }

  handleFilter(filter) {

    var scope = filter.scope || this.data.filter.scope;
    var query = (filter.query === '') ? filter.query : (filter.query || this.data.filter.query);
    var re;
    if (query) {
      try {
        re = new RegExp(query,'i');
      }
      catch(e) {
        re = false;  
      }
    }
    function applyQueryFilter(item, re){
      if (!re) {
        return true;
      }
      if (re.test(item.name)) {
        return true;
      } else {
        return false;
      }
    }

    var filtered = _.filter(this.data.all, (item) => {
      if (scope === 'rooms'){
        if (Utils.jid.is_room(item.jid)){
          return applyQueryFilter(item, re);
        } else {
          return false;
        }
      } else if(scope === 'people') {
        if (Utils.jid.is_private_chat(item.jid)){
          return applyQueryFilter(item, re);
        } else {
          return false;
        }
      } else {
        return applyQueryFilter(item, re);
      }
    });
    this.set({
      filtered: _.indexBy(filtered,'jid'),
      filter: {
        scope: scope || 'all',
        query: query || ''
      }
    });
  }

  handleRosterUpdate(roster) {
    this.set('all', this.assignAndSort(roster));
  }

  handleRoomsUpdate(rooms) {
    this.set('all',  this.assignAndSort(rooms));
  }

  handleRoomDeleted(jid) {
    delete this.data.all[jid];
    this.set('all', this.data.all);
  }

}

module.exports = new LobbyStore();



