var Utils = require('../helpers/utils');
var Store = require('../../lib/core/store');
var AppDispatcher = require("../dispatchers/app_dispatcher");
var PreferencesKeys = require("../keys/preferences_keys");

class PreferencesStore extends Store {

  getDefaults() {
    return {
      showUnreadDivider: true
    };
  }

  registerListeners() {
    AppDispatcher.registerOnce({
      'hc-init': (config) => {
        this.data = _.assign(this.getDefaults(), config.prefs, {features: config.features});
      }
    });
    AppDispatcher.register({
      'updated:active_chat': (jid) => {
        this.setChatToFocus(jid);
      },
      'updated:features': (data) => {
        this.data.features = data;
      },
      'save-preferences': (prefs) => {
        var hasChange = _.any(_.keys(prefs), (key) => {
          return !_.isEqual(this.data[key], prefs[key]);
        });
        if (hasChange) {
          this.set(prefs);
        }
      },
      'close-room': (data) => {
        this.removeRoom(data)
      },
      'toggle-sound-notifications': () => {
        this.toggleDisableSounds();
      }
    });

    this.on("change", () => {
      AppDispatcher.dispatch("updated:preferences", this.getAll());
    });
  }

  getAutoJoinRooms() {
    return this.get("autoJoin");
  }

  getChatToFocus() {
    return this.get("chatToFocus");
  }

  getDisableSounds() {
    return this.get(PreferencesKeys.DISABLE_SOUNDS);
  }

  toggleDisableSounds() {
    this.set(PreferencesKeys.DISABLE_SOUNDS, !this.getDisableSounds());
    this.savePrefs();
  }

  getHidePresenceMessages() {
    return this.get(PreferencesKeys.HIDE_PRESENCE_MESSAGES);
  }

  shouldUse24HrTime() {
    return this.get(PreferencesKeys.USE_24_HR_FORMAT) || false;
  }

  setChatToFocus(jid) {
    this.set({
      chatToFocus: jid ? jid.replace(/"/g,'') : ''
    });
  }

  removeRoom(data) {
    var autoJoinRooms = _.filter(this.getAutoJoinRooms(), (room) => {
      return room.jid !== data.jid;
    });
    AppDispatcher.dispatch('save-preferences', {
      autoJoin: autoJoinRooms
    });
  }

  savePrefs() {
    AppDispatcher.dispatch('save-preferences', this.getAll());
  }

}

module.exports = new PreferencesStore();


