var UserStatusAvatar = require('./user_status_avatar'),
    UserStatusMenu = require("./user_status_menu"),
    HelpMenu = require("./help_menu"),
    CurrentUserStore = require('../../stores/current_user_store'),
    AppHeaderActions = require('../../actions/app_header_actions'),
    SearchInput = require('./search_input'),
    PresenceIcon = require("../common/icon/presence-icon");

function getState(){
  return {
    user: CurrentUserStore.get('user'),
    presence: CurrentUserStore.get('presence'),
    presenceDialogVisible: false
  }
}

function setupFeedbackButton() {
  if (document.location.hostname !== "localhost") {
    $.ajax({
      url: "https://jira.atlassian.com/s/83f9ee60463444b76d4b2e62fed26460-T/en_UK2pighi/64005/131/1.4.17/_/download/batch/com.atlassian.jira.collector.plugin.jira-issue-collector-plugin:issuecollector/com.atlassian.jira.collector.plugin.jira-issue-collector-plugin:issuecollector.js?locale=en-UK&collectorId=12269b35",
      type: "get",
      cache: true,
      dataType: "script"
    });

    window.ATL_JQ_PAGE_PROPS = $.extend(window.ATL_JQ_PAGE_PROPS, {
      "triggerFunction": function (showCollectorDialog) {
        //Requires that jQuery is available!
        $("#give-feedback").click(function (e) {
          e.preventDefault();
          showCollectorDialog();
        });
      },
      "fieldValues": {
        "customfield_11030": window.versionID
      }
    });
  }
}

module.exports = React.createClass({

  displayName: "UserStatus",

  getInitialState: function() {
    return getState();
  },

  componentDidMount: function(){
    CurrentUserStore.on(['change'], this._onChange);
    setupFeedbackButton();
  },

  componentWillUnmount: function(){
    CurrentUserStore.off(['change'], this._onChange);
  },

  _onHelpButtonClicked: function() {
    AppHeaderActions.helpButtonClicked()
  },

  _onUserProfileDropdownClick: function() {
    AppHeaderActions.userProfileDropdownClicked();
  },

  render: function () {
    return (
      <div className="aui-header-secondary">
        <ul className="aui-nav">
          <li>
            <a href="javascript:void(0)" id="give-feedback" >
              <span className="aui-icon aui-icon-small aui-iconfont-comment"></span>
              &nbsp;Feedback
            </a>
          </li>
          <li>
            <SearchInput jid={this.props.active_chat} text={this.props.search_text} focus_search={this.props.focus_search}/>
          </li>
          <li>
            <a onClick={this._onHelpButtonClicked} className='aui-dropdown2-trigger hc-help' aria-owns="help-menu" aria-haspopup="true" aria-controls="help-menu">
              <span className="aui-icon aui-icon-small aui-iconfont-help"></span>
            </a>
            <HelpMenu />
          </li>
          <li className="status-dropdown" onClick={this._onUserProfileDropdownClick}>
            <a className='aui-dropdown2-trigger hc-header-user-avatar' aria-owns="current-user-status" aria-haspopup="true" aria-controls="current-user-status">
              <div className="aui-avatar-small">
                <UserStatusAvatar user={this.state.user} />
              </div>
              <PresenceIcon presence={this.state.presence.show} active={true} />
            </a>

            <UserStatusMenu user={this.state.user} onPresenceUpdate={this._onPresenceUpdate} onSignout={this._onSignout} onSettings={this._onSettings} />
          </li>
        </ul>
      </div>

      );
  },

  _onChange: function(){
    this.setState(getState());
  },

  _onPresenceUpdate: function(target) {
    AppHeaderActions.updatePresence(target.dataset);
  },

  _onPresenceUpdated: function(){
    AppHeaderActions.hideStatusMessageForm();
  },

  _onSignout: function(){
    AppHeaderActions.signout();
  },

  _onSettings: function() {
    AppHeaderActions.requestPreferencesDialog();
  }

});


