var PureRenderMixin = React.addons.PureRenderMixin;

module.exports = React.createClass({

  displayName: "RoomsNavItemRoom",

  mixins: [PureRenderMixin],

  render: function() {
    return (
      <a className="aui-nav-item" onClick={this.props.switchTab} title={this.props.name}>
        <span className={"aui-icon hipchat-icon-small icon-" + (this.props.privacy || 'dot')}></span>
        <span className="room-name">{this.props.name}</span>
        {this.props.unreadBadge}
      </a>
    );
  }
});


