var AppHeaderActions = require('../../actions/app_header_actions'),
    strings = require('../../strings/notification_banner_strings'),
    AppDispatcher = require('../../dispatchers/app_dispatcher'),
    appConfig = require('../../config/app_config');

module.exports = React.createClass({

  displayName: "NotificationBanner",

  getInitialState: function() {
    return {
      dismissed: false,
      shown: false
    };
  },

  componentWillReceiveProps: function (nextProps) {
    if (this.props.notification_preference && !nextProps.notification_preference) {
      this.setState({
        dismissed: true,
        shown: false
      });
    } else if (!this.props.notification_preference && nextProps.notification_preference) {
      this.setState({
        dismissed: false,
        shown: true
      });
      _.delay(() => {
        AppDispatcher.dispatch('position-invite-dialog');
        AppDispatcher.dispatch('notification-banner-status', {
          showing: true
        });
      }, appConfig.notification_banner_slide);
    } else if (nextProps.notification_dismissed > this.props.notification_dismissed) {
      this.setState({
        dismissed: true,
        shown: false
      });
    }
  },

  render: function () {
    var cx = React.addons.classSet,
        classes = cx({
          'dismissed': this.state.dismissed,
          'shown': this.state.shown,
          'aui-banner': true,
          'hc-banner-warning': true
        });
    return (
      <div className={classes} role="banner" aria-hidden="false">{strings.banner_message}&nbsp;
        <a onClick={this._enableNotifications}>{strings.enable_link}</a> {this.props.notification_dismissed > 0 ? strings.seriously_recommended : strings.recommended}
        <strong>&nbsp;&nbsp;&middot;&nbsp;&nbsp;</strong>
        <a onClick={this._dismiss}>{strings.dismiss_link}</a>
          {
            this.props.notification_dismissed > 0 ? <span>
              <strong>&nbsp;&nbsp;&middot;&nbsp;&nbsp;</strong>
              <a onClick={this._dismissForever}>{strings.dismiss_forever_link}</a>
            </span> : <span></span>
            }
      </div>
    );
  },

  _enableNotifications: function() {
    this._hideBanner();
    AppHeaderActions.requestNotificationPermission();
  },

  _dismiss: function() {
    this._hideBanner();
    AppHeaderActions.dismissNotificationBanner();
  },

  _dismissForever: function() {
    this._hideBanner();
    AppHeaderActions.dismissNotificationBannerForever();
  },

  _hideBanner: function() {
    this.setState({
      dismissed: true,
      shown: false
    });
    AppDispatcher.dispatch('notification-banner-status', {
      showing: false
    });
  }
});


