var ChatWindowActions = require('../../actions/chat_window_actions');
var Message = require('./message_types/message');
var NotificationMessage = require('./message_types/notification');
var InfoMessage = require('./message_types/info');
var FileMessage = require('./message_types/file');
var LinkMessage = require('./message_types/link');
var ImageMessage = require('./message_types/image');
var VideoMessage = require('./message_types/video');
var UserStateMessage = require('./message_types/user_state');
var TwitterStatusMessage = require('./message_types/twitter_status');
var TwitterUserMessage = require('./message_types/twitter_user');
var ClientPreferencesStore = require("../../stores/client_preferences_store");
var cx = React.addons.classSet;
var Utils = require('../../helpers/utils');

var message_types = {
  message: Message,
  notification: NotificationMessage,
  info: InfoMessage,
  file: FileMessage,
  link: LinkMessage,
  image: ImageMessage,
  video: VideoMessage,
  user_state: UserStateMessage,
  twitter_status: TwitterStatusMessage,
  twitter_user: TwitterUserMessage
};

module.exports = React.createClass({

  displayName: "ChatWindowMessageGroup",

  _getMessageType: function(message) {
    var MessageType = message_types[message.type];
    if (MessageType) return <MessageType key={message.mid} msg={message} _setScroll={this.props._setScroll} use24hrTime={this.props.use24hrTime} shouldHideGifs={this.props.shouldHideGifs}/>;
  },

  render: function() {
    var senderText = this.props.msg.sender;
    if (this.props.msg.type === 'image') {
      senderText = "Image";
    } else if (this.props.msg.isEmote) {
      senderText = "";
    }

    var lastViewedClass = (this.groupContainsLastViewed() && ClientPreferencesStore.shouldShowUnreadMessageDivider()) ? 'last-viewed' : '';
    var allFailed = (this.props.msg.status === 'failed' && !_.some(this.props.msg.messages, (msg) => { return msg.status !== 'failed' }));
    var show_time = !~['image', 'user_state', 'twitter_status', 'twitter_user', 'info'].indexOf(this.props.msg.type);
    var show_avatar = (this.props.show_avatars && this.props.msg.sender_avatar && this.props.msg.type != 'info');
    var time = show_time ? <div className="hc-chat-time">{this.props.msg.display_time}</div> : false;
    return (
      <div className={cx(_.object([
        ['hc-chat-row', true],
        [`hc-msg-${this.props.msg.color} hc-msg-${this.props.msg.type}`, true],
        ['fade', !this.props.msg.is_echo],
        ['hc-msg-failed', allFailed],
        ['show-avatars', this.props.show_avatars],
        [lastViewedClass, lastViewedClass]
      ]))} key={this.props.msg.mid}>
        <div className={cx({
          'hc-chat-from': true,
          'invisible': this.props.msg.type == 'user_state'
          })} onClick={this.onSenderClicked} data-mention={this.props.msg.sender_mention}>
          {show_avatar ?
            <span className="aui-avatar aui-avatar-medium" >
              <span className="aui-avatar-inner">
                <img src={this.props.msg.sender_avatar} width="32"></img>
              </span>
            </span>
            : false
          }
          {!this.props.show_avatars ? <span className="sender-name">{senderText}</span> : false}
        </div>
        <div className={cx({
          'hc-chat-msg': true,
          'image-link': this.props.msg.type == 'image'
        })}>
          {this.props.show_avatars ? <span className="sender-name">{senderText}</span> : false}
          {show_time && this.props.show_avatars ?
          <span>
            <span className="time-separator">&middot;</span>
            {time}
          </span>
            : {time}}
          {
            _.uniq(this.props.msg.messages).map((message) => {
              try {
                return this._getMessageType(message);
              } catch(e) {
                Utils.log('[ERROR]', message);
              }
            }, this)
          }
        </div>
      </div>

    );
  },
  onSenderClicked: function(e) {
    e.preventDefault();
    var mentionName = e.currentTarget.getAttribute('data-mention');
    if (mentionName) {
      ChatWindowActions.senderClick({mention: '@' + mentionName});
    }
  },

  groupContainsLastViewed: function() {
    return this.props.show_unread_divider && _.map(this.props.msg.messages, (msg) => {
        return msg.mid.split('-link')[0];
      }).indexOf(this.props.last_viewed_mid) != -1;
  }

});



