var NavPanelContent = require("./nav_panel_content");
var invariant = require('react/lib/invariant');

module.exports = React.createClass({

  displayName: "NavPanel",

  _onClick: function(e) {
    this.setState({
      activeTab: e.target.getAttribute("name")
    });
  },

  componentWillMount: function() {
    invariant(
      this.props.children.length > 1,
      'Require more than one tab'
    );

    var allChildrenNamed = _.every(this.props.children, function(child) {
      return child.props.name;
    });

    invariant(allChildrenNamed, "All NavPanel children must have a name attribute");
  },

  getInitialState: function() {
    return {
      activeTab: this.props.children[0].props.name
    };
  },

  render: function() {
    var menuItems = React.Children.map(this.props.children, (child) => {
      if (child.type.displayName == 'NavPanelContent') {
        return (
          <li role="tab" selected={this.state.activeTab === child.props.name} key={_.uniqueId()}>
            <button className="aui-button aui-button-link" onClick={this._onClick}
                    name={child.props.name}>{child.props.name}</button>
          </li>
        );
      }
    });
    var tabPanels = React.Children.map(this.props.children, (child) => {
      if (child.type.displayName == 'NavPanelContent') {
        return (
          <NavPanelContent name={child.props.name} selected={this.state.activeTab === child.props.name}
                           key={_.uniqueId()}>{child.props.children}</NavPanelContent>
        );
      }
    });

    return (
      <div {...this.props} className="hc-nav-panel aui-group">
        <ul className="aui-item aui-nav">
          {menuItems}
        </ul>
        <div className="aui-item">
          {tabPanels}
        </div>
      </div>
    );
  }

});


