var ChatHeaderActions = require('../../actions/chat_header_actions'),
    PureRenderMixin = React.addons.PureRenderMixin;

module.exports = React.createClass({

  displayName: "TopicInput",

  mixins: [PureRenderMixin],

  componentDidMount: function() {
    this.refs.topicInput.getDOMNode().select();
  },

  _handleTopicSubmit: function(e) {
    e.preventDefault();
    this._setTopic();
  },

  _onKeydown: function(evt) {
    if (evt.keyCode === 27) {
      this._dismissTopicEdit();
    }
  },

  _onKeyPressCancel: function(e) {
    if (e.keyCode === 13) {
      this._dismissTopicEdit();
    }
  },

  _onBlur: function(e) {
    var $relatedTarget = $(e.relatedTarget);
    var $form = $(this.refs.topicForm.getDOMNode());
    var containsRelatedTarget = $form.has($relatedTarget).length > 0;
    if (containsRelatedTarget && ($relatedTarget.is(".aui-button") || $relatedTarget.is(".topic-edit-input"))) {
      e.preventDefault();
    } else {
      this._dismissTopicEdit();
    }
  },

  _onValueChange: function(evt) {
    ChatHeaderActions.setInputValue({
      text: evt.target.value
    });
  },

  _setTopic: function () {
    var data = {
      jid: this.props.jid,
      topic: this.props.topic_input_value
    };
    ChatHeaderActions.changeTopic(data);
  },

  _dismissTopicEdit: function() {
    ChatHeaderActions.dismissTopicEdit();
  },

  render: function () {
    var cx = React.addons.classSet,
        inputClasses = cx({
          'text': true,
          'topic-edit-input': true
        });

    return (
      <form ref="topicForm" className="aui" onSubmit={this._handleTopicSubmit} onKeyDown={this._onKeydown}>
        <input type="text"
               ref="topicInput"
               className={inputClasses}
               value={this.props.topic_input_value}
               onBlur={this._onBlur}
               onChange={this._onValueChange}/>
        <button type="submit" className="aui-button" onBlur={this._onBlur}>
          <span className="aui-icon aui-icon-small aui-iconfont-success">Success </span>
        </button>
        <button type="button" className="aui-button" onBlur={this._onBlur} onKeyDown={this._onKeyPressCancel} onClick={this._dismissTopicEdit}>
          <span className="aui-icon hipchat-icon-xsmall hc-close-icon icon-close">Close </span>
        </button>
      </form>
    );
  }
});


