var AutoCompleteBox = require('../common/autocomplete/auto_complete_box');
var ChatInputStore = require('../../stores/chat_input_store');
var Utils = require('../../helpers/utils');

var getInputState = function () {
  var cis = ChatInputStore.getAll(),
    emoticons = _.toArray(cis.emoticons),
    defaultEmoticons = _.filter(_.toArray(cis.smileys), (smileys) => {
      return smileys.shortcut.indexOf('(') === 0;
    });
  emoticons = _.sortBy(emoticons.concat(defaultEmoticons), 'shortcut');
  return {
    emoticons: emoticons,
    path_prefix: cis.path_prefix
  };
};

module.exports = React.createClass({

  displayName: "EmoticonAutoComplete",

  getInitialState: function(){
    return getInputState();
  },

  getDefaultProps: function(){
    return {
      emoticon_text: '',
      selected_item: 0
    }
  },

  componentDidMount: function(){
    ChatInputStore.on('change:emoticons', this._onChange);
  },

  componentWillUnmount: function(){
    ChatInputStore.off('change:emoticons', this._onChange);
  },

  shouldComponentUpdate: function(nextProps){
    return this.props.emoticon_text !== nextProps.emoticon_text
      || this.props.selected_item !== nextProps.selected_item;
  },

  componentDidUpdate: function(){
    this.props.updateEmoticonResultsCount(this._resultsCount);
  },

  render: function() {
    var emoticon_text = this.props.emoticon_text.toLowerCase(), filtered = [];
    if (emoticon_text.indexOf('(') === 0) {
      filtered = _.filter(this.state.emoticons, (emoticon) => {
        return emoticon.shortcut.indexOf(emoticon_text) === 0;
      });
    }
    this._resultsCount = filtered.length;
    return (
      <div className="hc-emoticon-ac hc-chat-input-autocomplete">
        <AutoCompleteBox key="emoticon_auto_complete" list={filtered}
          textarea_ref={this.props.textarea_ref}
          getItemContent={this._getItemContent}
          selected_item={this.props.selected_item}
          onItemSelected={this.props.onEmoticonSelected}
          ref="acBox" />
      </div>
    );
  },

  _getItemContent: function(emoticon){
    return (
      <div>
        <div className="hc-ac-preview">
          <img src={this.state.path_prefix + '/' + emoticon.file} width="20px"/>
        </div>
        <span className="hc-ac-name">{emoticon.shortcut}</span>
      </div>
    );
  },

  _onChange: function(){
    this.setState(getInputState());
  }

});



