var Utils = require("../../helpers/utils");
var FileViewerActions = require('../../actions/file_viewer_actions');
var fileHelper = require('../../helpers/file_helper');

module.exports = React.createClass({

  displayName: "RightSideBarFile",

  render: function () {
    return (
      <li className="hc-roster-item" >
        <div className="aui-nav-item" title={fileHelper.basename(this.props.file.name)}>
          <a className="hc-file-link" target="_blank" href={this.props.file.file_url} onClick={this._onClick}>
            <span className={"hc-file-icon " + this.props.file.icon_class}></span>
            <span className="hc-file-name">{fileHelper.basename(this.props.file.name)}</span>
          </a>
          <span className="hc-roster-user-name">{this.props.file.user_name}</span>
          <span className="hc-roster-date">{this.props.time}</span>
        </div>
      </li>
    );
  },

  _onClick: function (evt) {
    if (fileHelper.shouldOpenFileViewer(evt)) {
      evt.preventDefault();
      FileViewerActions.openInFileViewer(this.props.file);
    }
  }
});



