var ModalDialog = require("../../common/modal_dialog/modal_dialog");
var KeyboardShortcutStrings = require("../../../strings/keyboard_shortcut_strings");

module.exports = React.createClass({

  displayName: "KeyboardShortcutsDialog",

  getDefaultProps: function() {
    return {
      shortcuts: {}
    };
  },

  _getKeyItem: function (shortcut) {
    var keyAction = this._getKeyLayout(shortcut.keys);
    return (
      <li className="hc-list-item">
        <span className="hc-list-item-desc">{shortcut.title}</span>
        {keyAction}
      </li>
    )
  },

  _getKeyLayout: function (keys) {
    return (
      <span className="hc-list-item-action">
        {
          _.map(keys, function(item, index) {
            return this._getKey(item, index);
          }, this)
        }
      </span>
    )
  },

  _getKey: function (key, index) {
    var keyClass = "hc-key-" + key.type,
        name = key.name;
    if (index !== 0) {
      return (
        <span>
          <span className="hc-key-separator">{KeyboardShortcutStrings.separator}</span>
          <kbd className={keyClass}>{name}</kbd>
        </span>
      );
    } else {
      return (
        <kbd className={keyClass}>{name}</kbd>
      );
    }
  },

  _dialogBody: function() {
    return (
      <ul className="hc-list">
        {
          _.map(this.props.shortcuts, function(shortcut) {
            return this._getKeyItem(shortcut);
          }, this)
        }
      </ul>
    );
  },

  render: function() {
    return (
      <ModalDialog dialogId="keyboard-shortcuts-dialog" title="Keyboard Shortcuts" dialogBody={this._dialogBody} size="small"/>
    );
  }
});


