// Depends on Lodash

var cookies = require('./cookies');

class Storage {

  constructor() {
    this.prefix = 'hc-';
    this.locations = ['local', 'session', 'global'];
    try {
      return 'localStorage' in window && window['localStorage'] !== null;
    } catch (e) {
      window.localStorage = window.sessionStorage = cookies;
    }
  }

  has(key) {
    return _.first(this.locations, (location) => {
      return _.keys(window[(location + 'Storage')]).indexOf(this.prefix + key) != -1;
    })[0];
  }

  get(key) {
    var location = this.has(key);
    if (location) {
      try {
        return JSON.parse(window[(location + 'Storage')].getItem(this.prefix + key).toString());
      } catch (e) {
        return false;
      }
    }
    return false;
  }

  set(key, val, location) {
    return window[((location || "local") + 'Storage')].setItem(this.prefix + key, JSON.stringify(val, function(key, value) {
      if (value instanceof RegExp) {
        return value.toString();
    }
    return value;
    }));
  }

  unset(key) {
    var location = this.has(key);
    if (location) {
      window[(location + 'Storage')].removeItem(this.prefix + key);
      return true;
    }
    return false;
  }

  clear() {
    _.each(this.locations, (location) => {
      _.filter(_.keys(window[(location + 'Storage')]), (key) => {
        return key.match(this.prefix);
      }).forEach((key) => {
        window[(location + 'Storage')].removeItem(key);
      });
    });
  }

}

module.exports = new Storage();


