var RoomActions = require("./room_actions"),
    TopicInput = require("./topic_input"),
    ChatHeaderActions = require('../../actions/chat_header_actions'),
    Utils = require("../../helpers/utils"),
    strings = require('../../strings/chat_header_strings');

module.exports = React.createClass({

  displayName: "GroupChatHeader",

  _getTopic: function () {
    var topic;
    if (this.props.topic_editing) {
      topic = this._getTopicInput();
    } else {
      topic = this._getTopicText();
    }
    return topic;
  },

  _getTopicInput: function () {
    return <TopicInput jid={this.props.info.jid} topic_input_value={this.props.topic_input_value}/>;
  },

  _getTopicText: function () {
    var topic,
        escapedTopic;
    if (this.props.info.topic) {
      escapedTopic = Utils.escapeAndLinkify(this.props.info.topic, {
        escape_whitespace: true,
        do_word_breaks: false,
        do_emoticons: this.props.do_emoticons
      });
      topic = <p title={this.props.info.topic} onDoubleClick={this._onTopicDoubleClick} dangerouslySetInnerHTML={{__html: '<span>' + escapedTopic + '</span>'}}></p>
    } else {
      topic = <i onDoubleClick={this._onTopicDoubleClick}>{strings.default_topic}</i>;
    }
    return topic;
  },

  _onTopicDoubleClick: function() {
    ChatHeaderActions.handleTopicDoubleClick();
  },

  render: function () {
    var cx = React.addons.classSet,
        iconClasses = cx({
          'aui-icon': true,
          'hipchat-icon-huge': true,
          'icon-private': (this.props.info.privacy === "private"),
          'icon-public': (this.props.info.privacy === "public"),
          'icon-dot': (!this.props.info.privacy)
        }),
        topic = this._getTopic();

    return (
      <div className="aui-page-header-inner hc-groupchat">
        <div className="page-header-icon">
          <span className={iconClasses}></span>
        </div>
        <div className="aui-page-header-main hc-page-header-main">
          <h3>{this.props.info.name}</h3><br />
          {topic}
        </div>
        <RoomActions info={this.props.info} type={this.props.type} groupchat_active_panel={this.props.groupchat_active_panel} onPanelSelect={this.props.onPanelSelect}/>
      </div>
    );
  }
});


