var AutoComplete = require("./auto_complete");
var TextareaCaretMirror = require("./textarea_caret_mirror");

module.exports = React.createClass({

  displayName: "AutoCompleteBox",

  render: function() {
    var self = this;
    var results = this.props.list.map(function(item, idx){
      var isSelected = false;
      if (idx === self.props.selected_item) isSelected = true;
      return <AutoComplete item={item}
        key={idx}
        idx={idx}
        getItemContent={self.props.getItemContent}
        selected={isSelected}
        onItemSelected={self.props.onItemSelected}
        ref={'item-' + idx}/>;
    });
    var classes = React.addons.classSet({
      'aui-inline-dialog': true,
      'show': this.props.list.length > 0
    });
    var caretOptions = {
      textarea_ref: this.props.textarea_ref,
      ac_box_ref: this.refs.acBox,
      ac_box_container_ref: this.refs.container,
      arrow_offset_left: 30,
      arrow_offset_top: 10
    };
    var styles = {
      'left': caretOptions.arrow_offset_left + 'px',
      'top': caretOptions.arrow_offset_top + 'px'
    };
    return (
      <div>
        <div className={classes} style={styles} ref="acBox">
          <div className="aui-inline-dialog-contents contents" ref="container">
            <ul>
            {results}
            </ul>
          </div>
          <div className="aui-inline-dialog-arrow arrow aui-css-arrow aui-bottom-arrow"></div>
        </div>
        <TextareaCaretMirror ref="caret" opts={caretOptions} />
      </div>
    );
  }

});



