var Store = require("../../lib/core/store");
var ClientPreferencesStore = require("../stores/client_preferences_store");
var AppDispatcher = require("../dispatchers/app_dispatcher");

class LayoutStore extends Store {

  constructor() {
    this.data = this.getDefaults();
    this.register_listeners();
  }

  getDefaults() {
    return {
      ready: false,
      bannerShowing: false,
      leftColumnWidth: this.getLeftColumnWidth()
    }
  }

  register_listeners() {
    AppDispatcher.register({
      'app-state-ready': () => {
        this.set("ready", true);
      },
      'notification-banner-status': (data) => {
        this.set("bannerShowing", data.showing);
      }
    });
  }

  getLeftColumnWidth() {
    return ClientPreferencesStore.getLeftColumnWidth();
  }
}

module.exports = new LayoutStore();


