var AppDispatcher = require('../../../dispatchers/app_dispatcher'),
    TooltipActions = require('../../../actions/tooltip_actions'),
    TooltipStrings = require('../../../strings/tooltip_strings');

module.exports = React.createClass({

  displayName: "TooltipTypeSmileySelector",

  getDefaultProps: function() {
    return {
      data: {
        smileys : {},
        prefix: '',
        user_is_admin: false
      }
    };
  },

  render: function () {
    var custom_link;
    if (this.props.data.user_is_admin) {
      custom_link = <div className="hc-float-right menu-link"><a target="_blank" href="/admin/emoticons">{TooltipStrings.custom_emoticon_link}</a></div>;
    }

    return (
      <div className="smileys-selection">
    {_.map(this.props.data.smileys, (smiley) => {
      return <img data-shortcut={smiley.shortcut} height={smiley.height} width={smiley.width} onClick={this._onSmileyClick} key={smiley.file} src={this.props.data.prefix + "/" + smiley.file} />;
    }, this)}
        <div className="clear"></div>
        <div className={this.props.data.user_is_admin ? "hc-float-left menu-link" : "hc-text-center menu-link"}>
          <a target="_blank" href="/emoticons">{TooltipStrings.emoticon_more_link}</a>
        </div>
        {custom_link}
      </div>
    )
  },

  _onSmileyClick: function (e) {
    TooltipActions.smileyChosen({shortcut : e.currentTarget.getAttribute('data-shortcut')});
  }

});


