var InlineDialog = require("../common/inline_dialog/inline_dialog");
var PresenceIcon = require("../common/icon/presence-icon");
var Utils = require("../../helpers/utils");
var AppHeaderActions = require("../../actions/app_header_actions");

module.exports = React.createClass({

  displayName: "UserStatusMessageForm",

  getDefaultProps: function() {
    return {
      visible: false
    }
  },

  _getStatusInput: function() {
    return this.getDOMNode().querySelectorAll("input[name='status-message']")[0];
  },

  _onSubmit: function(e) {
    e.preventDefault();
    var input = this._getStatusInput();
    AppHeaderActions.setStatus({
      show: this.props.presence.show,
      status: Utils.escape(input.value)
    });
  },

  _onShow: function() {
    var input = this._getStatusInput();
    input.value = "";
    input.focus();
  },

  render: function() {
    return (
      <InlineDialog dialogId="userStatusMessage" onShow={this._onShow}>
        <form id="userStatusForm" className="aui aui-group" onSubmit={this._onSubmit}>
          <div className="aui-item status-icon">
            <PresenceIcon presence={this.props.presence.show} />
          </div>
          <div className="aui-item status-message">
            <input type="text" className="text" placeholder="Set your status" name="status-message" />
          </div>
          <div className="aui-item">
            <button className="aui-button aui-button-small" type="submit">OK</button>
          </div>
        </form>
      </InlineDialog>
    );
  }

});


