
module.exports = React.createClass({

  displayName: "UserStatusAvatar",

  render: function() {
    return (
      <div className="aui-avatar-inner hc-user-photo">
        { this.props.user.photo_small ?
          <img src={this.props.user.photo_small} />:
          <span className="aui-icon hc-user-silhouette"></span>
        }
      </div>
    );
  }
});


