var AuiCheckboxField = require("../../common/aui/form/aui_checkbox_field");
var PreferencesStore = require("../../../stores/preferences_store");
var ClientPreferencesStore = require("../../../stores/client_preferences_store");
var PreferencesKeys = require("../../../keys/preferences_keys");

module.exports = React.createClass({

  displayName: "PreferenceCheckboxField",

  getDefaultProps: function() {
    return {
      isReverse: false
    }
  },

  _isDefaultChecked: function() {
    var isServerPref = _.contains(_.values(PreferencesKeys), this.props.id);
    var store = (isServerPref) ? PreferencesStore : ClientPreferencesStore;
    var isChecked = store.get(this.props.id);

    if (this.props.isReverse) {
      isChecked = !isChecked;
    }

    return isChecked;
  },

  render: function() {
    return (
      <AuiCheckboxField {...this.props} defaultChecked={this._isDefaultChecked()} />
    );
  }
});


