var PresenceIcon = require("../common/icon/presence-icon");

module.exports = React.createClass({

  displayName: "UserStatusAvatar",

  componentDidMount: function() {
    // Can't use standard React events here since AUI has mutated the drop-down.
    // React can't operate on DOM that's mutated since it's been created. In most
    // other cases, we should stick with React events.
    var statusActions = document.querySelectorAll('#current-user-status .hc-availability a'),
        signoutLink = document.querySelectorAll('.signout a');

    for(var i=0;i<statusActions.length;i++){
      statusActions[i].addEventListener('click', function(e){
        this.props.onPresenceUpdate.bind(null, e.currentTarget)();
      }.bind(this));
    }
    signoutLink[0].addEventListener('click', function(e){
        this.props.onSignout.bind(null, e.currentTarget)();
      }.bind(this));
    var dialogLink = document.querySelectorAll(".settings > a");
    dialogLink[0].addEventListener('click', (e) => {
      this.props.onSettings.bind(null, e.currentTarget)();
    });
  },
  render: function () {
    return (

      <div className="aui-dropdown2 aui-style-default aui-dropdown2-in-header" id="current-user-status" aria-hidden="true" data-dropdown2-alignment="right">
        <div className="aui-dropdown2-section">
          <ul>
            <li>
              <a className="hc-user-name" href="/account" target="_blank">{this.props.user.user_name}</a>
            </li>
          </ul>
        </div>
        <div className="aui-dropdown2-section hc-availability">
          <ul>
            <li className="chat aui-inline-dialog-trigger">
              <a data-show="chat"><PresenceIcon presence="chat" /> Available</a>
            </li>
            <li className="xa aui-inline-dialog-trigger">
              <a data-show="xa"><PresenceIcon presence="xa" /> Away</a>
            </li>
            <li className="dnd aui-inline-dialog-trigger">
              <a data-show="dnd"><PresenceIcon presence="dnd" /> Do not disturb</a>
            </li>
          </ul>
        </div>
        <div className="aui-dropdown2-section hc-actions">
          <ul>
            <li className="settings">
              <a><span className="aui-icon aui-icon-small aui-iconfont-configure"></span> Settings</a>
            </li>
            <li className="signout">
              <a><span className="aui-icon aui-icon-small aui-iconfont-devtools-checkout"></span> Sign out</a>
            </li>
          </ul>
        </div>
      </div>
    );
  }
});


