var HeaderStrings = require("../../strings/app_header_strings"),
    AppHeaderActions = require("../../actions/app_header_actions"),
    KeyboardShortcuts = require("../../helpers/keyboard_shortcuts"),
    DialogActions = require("../../actions/dialog_actions");

module.exports = React.createClass({

  displayName: "HelpMenu",

  componentDidMount: function() {
    // Can't use standard React events here since AUI has mutated the drop-down.
    // React can't operate on DOM that's mutated since it's been created. In most
    // other cases, we should stick with React events.
    document.querySelector(".hc-keyboard-shortcuts").addEventListener("click", this._onKeyboardShortcutsClick);
  },

  componentWillUnmount: function () {
    document.querySelector(".hc-keyboard-shortcuts").removeEventListener("click", this._onKeyboardShortcutsClick);
  },

  _onKeyboardShortcutsClick: function () {
    var shortcuts = KeyboardShortcuts.getShortcuts();
    DialogActions.showKeyboardShortcutsDialog(shortcuts);
  },

  render: function () {
    return (
      <div className="aui-dropdown2 aui-style-default aui-dropdown2-in-header" id="help-menu" aria-hidden="true" data-dropdown2-alignment="left">
        <div className="aui-dropdown2-section">
          <ul>
            <li>
              <a className="hc-online-help" href="http://help.hipchat.com/" target="_blank">{HeaderStrings.online_help}</a>
            </li>
            <li>
              <a className="hc-keyboard-shortcuts">{HeaderStrings.keyboard_shortcuts}</a>
            </li>
          </ul>
        </div>
      </div>
    );
  }
});


