module.exports = {
  /*
  These two arrays correspond to each other; they must be
  the same length and the two corresponding messages must be
  at the same index.
  */
  happy: [
    'Hold tight. Shouldn\'t take too long.',
    'Just a sec.',
    'One HipChat, coming right up.',
    'Relax. We got this.',
    'Here it comes.',
    'Let\'s get started.',
    'Workin\' on it.'
  ],
  sad: [
    'Yikes. This really is taking a while...',
    'Looks like a sec wasn\'t enough. Give us a minute...',
    'Well... coming up soon...',
    'Oh man. Don\'t freak out, we still got this...',
    'This hardly ever happens...',
    'Looks like we\'ll be starting a little late...',
    'Still workin\' here... You sure do look nice today...'
  ]
};


