var RoomActions = require("./room_actions"),
    PresenceIcon = require("../common/icon/presence-icon"),
    Icon = require("../common/icon/icon"),
    strings = require('../../strings/chat_header_strings'),
    Utils = require("../../helpers/utils");

module.exports = React.createClass({

  displayName: "OTOHeader",

  _getAvatarStyle: function () {
    return {
      backgroundImage: "url(" + this.props.info.photo_large + ")"
    }
  },

  _getTitle: function () {
    var classes,
        title = <span></span>;
    if (this.props.info.title) {
      classes = {
        "hc-separator-icon": true
      };
      title = <span className="hc-oto-header-title"><Icon classes={classes} iconName="dot" />{this.props.info.title}</span>
    }
    return title;
  },

  _getHeaderIcon: function () {
    var icon,
        avatarStyle = this._getAvatarStyle();
    if (!this.props.info.photo_large || /\/img\/silhouette_125\.png/.test(this.props.info.photo_large)) {
      icon = <span className="aui-icon hc-priv-chat hc-user-silhouette"></span>;
    } else {
      icon = <span className="aui-icon hc-priv-chat hc-user-avatar" style={avatarStyle}></span>
    }
    return icon;
  },

  _getStatus: function () {
    var status;
    if (this.props.info.loading_profile) {
      status = strings.loading;
    } else {
      status = this.props.info.presence.status || Utils.user.chat_header_status(this.props.info.presence.show);
    }
    return status;
  },

  render: function() {
    var cx = React.addons.classSet,
        hiddenClasses = cx({
          'hidden': this.props.info.loading_profile
        }),
        mentionClasses = cx({
          'hidden': this.props.info.loading_profile,
          'mention-name': true
        }),
        title = this._getTitle(),
        headerIcon = this._getHeaderIcon(),
        status = this._getStatus();

    return (
      <div className="aui-page-header-inner hc-priv-chat">
        <div className="page-header-icon">
          {headerIcon}
        </div>
        <div className="aui-page-header-main hc-page-header-main">
          <h3>{this.props.info.name}</h3>&nbsp;<span className={mentionClasses}>{'(@' + this.props.info.mention_name + ')'}</span>
          <p>
            <PresenceIcon presence={this.props.info.presence.show} />
            <span className="hc-oto-header">{status}</span>
            <span className={hiddenClasses}>
              <Icon classes={{"hc-separator-icon": true}} iconName="dot" />
              <span>{this.props.info.time}</span>
              {title}
              <span>
                <Icon classes={{"hc-separator-icon": true}} iconName="dot" />
                <a href={"mailto:" + this.props.info.email}>{this.props.info.email}</a>
              </span>
            </span>
          </p>
        </div>
        <RoomActions info={this.props.info} type={this.props.type} chat_active_panel={this.props.chat_active_panel} onPanelSelect={this.props.onPanelSelect}/>
      </div>
    );
  }

});


