var AppDispatcher = require('../dispatchers/app_dispatcher'),
  Store = require("../../lib/core/store"),
  Utils = require('../helpers/utils'),
  PreferencesStore = require('../stores/preferences_store'),
  notifier = require('../helpers/notifier'),
  AnalyticsDispatcher = require('../dispatchers/analytics_dispatcher');

class RoomsNavStore extends Store {

  constructor() {

    this.data = this.getDefaults();

    this.registerListeners();
    this.savePrefsDebounced = _.debounce(this.savePrefs, 2000, {leading: true, trailing: false});

    this.windowFocused = true;
  }

  getDefaults() {
    return {
      mentionRegex: null,
      current_user: {},
      active_chat: 'lobby',
      active_chat_type: '',
      rooms: {
        people: [],
        rooms: []
      },
      room_order: []
    };
  }

  registerListeners() {
    AppDispatcher.registerOnce({
      'hc-init': (data) => {
        this.handleRoomsInit(data);
      }
    });
    AppDispatcher.register({
      'initial-select-room': () => {
        this.hc_init_complete = true;
      },
      'before:select-room': (data) => {
        this.set('active_chat', data.jid);
      },
      'updated:current_user': (user) => {
        this.data.mentionRegex = new RegExp('@(' + user.mention + '\\b|all\\b|here\\b|"' + user.user_name + '")', 'ig');
        this.set('current_user', user);
      },
      'updated:newMessages': (messages) => {
        this.handleMessageUpdate(messages);
      },
      'updated:roster': (roster) => {
        this.handleRosterUpdate(roster);
      },
      'updated:preferences': (prefs) => {
        this.handleSelectRoom(prefs.chatToFocus);
      },
      'updated:activeRooms': (rooms) => {
        this.handleRoomsUpdate(rooms);
      },
      'updated:active_chat': (jid) => {
        this.handleSelectRoom(jid);
      },
      'open-room': (data) => {
        this.handleOpenRoom(data);
      },
      'room-closed': (data) => {
        this.handleRoomClosed(data.jid);
      },
      'update-room-order': (room_jids) => {
        this.updateRoomOrder(room_jids);
      },
      'navigate-rooms': (data) => {
        this.navigateRooms(data.direction);
      },
      'application-focused': () => {
        this.windowFocused = true;
        this.clearUnreadCountForCurrentChat();
      },
      'application-blurred': () => {
        this.windowFocused = false;
      }
    });
  }

  /**
   * Helper method for grouping tabs (rooms first then people)
   * @param {object} tabs
   */
  groupRooms(tabs) {
    tabs = _.groupBy(_.reject(tabs, function (item) {
        return !item || item.name === '';
      }), function (chat) {
      return chat.type == 'groupchat' ? 'rooms' : 'people';
    });
    return tabs;
  }

  /**
   * Helper method for ordering rooms
   * @param {array} rooms
   */
  orderRooms(rooms) {
    if (this.data.room_order.length) {
      var results = _.compact(_.map(this.data.room_order, function (jid) {
        if (rooms[jid]) {
          return rooms[jid];
        }
      }, this));
      var newRooms = _.map(rooms, function (room) {
        var newRoom = _.find(results, function (item) {
          return room.jid === item.jid;
        });
        if (!newRoom) {
          return room;
        }
      });
      return _.compact(results.concat(newRooms));
    } else {
      return rooms;
    }
  }

  /**
   * Helper method for setting room_order array based on autoJoin or RoomsNavStore rooms
   * @param {array} autoJoin
   */
  setRoomOrder(autoJoin) {
    var jids;
    if (autoJoin) {
      jids = _.map(autoJoin, function (room) {
        return room.jid;
      }, this);
    } else {
      jids = _.flatten(_.map(['rooms', 'people'], function(groupName) {
        return _.map(this.data.rooms[groupName], function (room) {
          return room.jid;
        }, this);
      }, this));
    }
    this.set({
      room_order: jids
    });
  }

  handleRoomsInit() {
    var autoJoinRooms = PreferencesStore.getAutoJoinRooms();
    if (!_.isEmpty(autoJoinRooms)) {
      this.setRoomOrder(autoJoinRooms);
    }
    var chatToFocus = PreferencesStore.getChatToFocus();
    if (chatToFocus) {
      this.set({
        active_chat: chatToFocus
      });
    }
  }

  /**
   * Handle message stanzas that arrive either in bulk or
   * one at a time
   * @param {object} messageData
   */
  handleMessageUpdate(messageData) {
    var chat;

    _.forEach(messageData, (messages, jid) => {
      _.forEach(this.data.rooms, (group, type) => {
        chat = _.find(group, room => {
          return room.jid == jid;
        });
        _.forEach(messages, (msg) => {
          if (msg.delay || (msg.room === this.data.active_chat && this.windowFocused) || msg.sender === this.data.current_user.user_name) return;
          if (chat &&
            !msg.is_history_message &&
            msg.type != 'user_state') {
            ++chat.unreadCount;
            if (this.data.mentionRegex.test(msg.body) || type == 'people') {
              chat.hasMention = true;
              return false;
            }
          }
        });
      });

    });
    this.set('rooms', this.data.rooms);
    this.setRoomOrder();
    this._updateTotalUnreadCount();

  }

  _updateTotalUnreadCount() {
    var hasMention = false;
    function unreadCB(r) {
      if (!hasMention && r.hasMention) {
        hasMention = true;
      }
      return r.unreadCount;
    }

    var unreadTotal = _.reduce(
      _.map(this.data.rooms.rooms, unreadCB).concat(
        _.map(this.data.rooms.people, unreadCB)
      ), function (s, n) {
        return s + n
      }
    );

    notifier.updateTotalUnreadCount(unreadTotal, hasMention);
  }

  handleRosterUpdate(roster) {
    var room;
    _.map(roster, (user, jid) => {
      room = _.find(this.data.rooms['people'], room => {
        return room.jid == jid;
      });
      if (room) {
        _.assign(room.presence, roster[jid].presence);
        // Ensure OTO Chats have a name
        if (!room.name) {
          if (Utils.dot(user, "name")) {
            room.name = user.name;
          }
        }
      }
    });

    this.set({
      rooms : this.data.rooms
    });
    this.setRoomOrder();
  }

  handleOpenRoom(data) {
    if (_.find(this.data.rooms.rooms, {jid: data.jid}) || _.find(this.data.rooms.people, {jid: data.jid})) {
      this.handleSelectRoom(data.jid);
    } else {
      AppDispatcher.dispatch('join-room', {
        jid: data.jid,
        name: data.name
      });
      this.handleSelectRoom(data.jid);
    }
  }

  /**
   * Handle rooms update - accepts activeRooms obj with jids as keys
   * @param {object} activeRooms
   */
  handleRoomsUpdate(activeRooms) {
    var rooms,
        orderedRooms,
        roomDefault = {
          unreadCount: 0,
          hasMention: false
        };
    _.map(activeRooms, (room) => {
      _.defaults(room, roomDefault);
    });
    _.reject(activeRooms, (room) => {return !room;});
    orderedRooms = this.orderRooms(activeRooms);
    rooms = this.groupRooms(orderedRooms);
    _.assign(this.data, {
      rooms: _.defaults(rooms, {rooms: [], people: []}),
      active_chat: this.data.active_chat
    });
    if (!activeRooms[this.data.active_chat]) {
      this.data.active_chat = 'lobby'
    }
    this.saveRooms(orderedRooms, this.data.active_chat, this.data.rooms)
    this._updateTotalUnreadCount();
  }

  getNextRoom(jid) {
    var nextRoom,
        idx;
    if (this.data.room_order.length) {
      idx = this.data.room_order.indexOf(jid);
      if (idx !== -1) {
        if (idx == 0) {
          idx++;
        } else {
          idx--;
        }
        nextRoom = this.data.room_order[idx];
      }
    }
    return nextRoom || "lobby";
  }

  handleRoomClosed(jid) {
    if (jid === this.data.active_chat) {
      var data = {
        jid: this.getNextRoom(jid),
        isClose: true
      };
      AppDispatcher.dispatch('select-room', data);
      AnalyticsDispatcher.dispatch('analytics-select-room', data);
    }
  }

  handleSelectRoom(jid) {
    var selectedJid = _.find(this.data.rooms.rooms,{jid:jid}) || _.find(this.data.rooms.people,{jid:jid}) ? jid : 'lobby';
    _.map(this.data.rooms, (group) => {
      _.assign(_.find(group, (room) => {
        return room.jid == jid
      }), {
        unreadCount: 0,
        hasMention: false
      });
    });
    this.set({
      rooms : this.data.rooms,
      active_chat : selectedJid
    });
    this.setRoomOrder();
    if (this.hc_init_complete) {
      this.savePrefs({
        chatToFocus: selectedJid
      });
    }
    this._updateTotalUnreadCount();
  }

  clearUnreadCountForCurrentChat(){
    if (this.data.active_chat === 'lobby') return;
    var jid = this.data.active_chat;
    _.map(this.data.rooms, (group) => {
      _.assign(_.find(group, (room) => {
        return room.jid == jid
      }), {
        unreadCount: 0,
        hasMention: false
      });
    });
    this.set({
      rooms: this.data.rooms
    });
    this._updateTotalUnreadCount();
  }

  navigateRooms(direction) {
    var newJid,
        roomIndex = _.findIndex(this.data.room_order, function(jid) {
          return jid === this.data.active_chat;
        }, this);

    if (direction === "up") {
      if (roomIndex === 0) {
        newJid = "lobby";
      } else {
        newJid = this.data.room_order[roomIndex - 1];
      }
    } else if (direction === "down") {
      if (roomIndex === -1 && this.data.active_chat === "lobby") {
        newJid = this.data.room_order[0];
      } else {
        newJid = this.data.room_order[roomIndex + 1];
      }
    }
    if (newJid) {
      AppDispatcher.dispatch('select-room', {
        jid: newJid
      });
    }
  }

  /* Takes array of room jids */
  updateRoomOrder(jids) {
    var currentRoomData = [],
        fullRoomData,
        rooms;

    currentRoomData = currentRoomData.concat(this.data.rooms["rooms"], this.data.rooms["people"]);
    fullRoomData = _.map(jids, function (jid) {
      return _.find(currentRoomData, function (item) {
        return item.jid === jid;
      });
    }, this);
    rooms = _.groupBy(fullRoomData, function (chat) {
      return chat.type == 'groupchat' ? 'rooms' : 'people';
    });
    this.saveRooms(fullRoomData, this.data.active_chat, rooms);
  }

  saveRooms(roomData, activeChat, roomObj) {
    var autoJoin = _.map(roomData, function (room) {
      return {
        jid: room.jid,
        name: room.name
      }
    });
    var archived = _.pluck(_.filter(this.data.rooms.rooms, function (room) {
      return !!parseInt(room.is_archived);
    }), 'jid');
    this.set({
      rooms: roomObj,
      active_chat: activeChat
    });
    if (this.hc_init_complete) {
      this.savePrefsDebounced({
        autoJoin: (autoJoin.length) ? _.reject(autoJoin, (room) => {return !!~archived.indexOf(room.jid)}) : false,
        chatToFocus: activeChat
      });
      this.setRoomOrder();
    }
  }

  savePrefs(data) {
    AppDispatcher.dispatch('save-preferences', data);
  }

}

module.exports = new RoomsNavStore();


