var PureRenderMixin = React.addons.PureRenderMixin;

module.exports = React.createClass({

  displayName: "LobbyNavItem",

  mixins: [PureRenderMixin],

  render: function() {
    var cx = React.addons.classSet;
    var classes = cx({
      'hc-tab': true,
      'aui-nav-selected': this.props.active
    });
    return (
      <li className={classes} >
        <a className="aui-nav-item aui-nav-selected" onClick={this.props.switchTab}>
          <span className={"aui-icon hipchat-icon-small icon-lobby"}></span>
          <span className="aui-nav-item-label">Lobby</span>
        </a>
      </li>
    );
  }
});


