module.exports = React.createClass({

  displayName: "LinkMessageType",

  shouldComponentUpdate: function(nextProps, nextState) {
    return !_.isEqual(this.props, nextProps);
  },

  render: function () {
    return (
      <div className="msg-line" data-mid={this.props.msg.mid} >
        <img src={this.props.msg.link_details.favicon_url} />
        <b>{this.props.msg.link_details.header_text + " - "}</b>
        <a href={this.props.msg.link_details.url} target="_blank">{this._getHostname(this.props.msg.link_details.full_url || this.props.msg.link_details.url)}</a>
        <div>{this.props.msg.link_details.desc}</div>
      </div>
    );
  },

  _getHostname: function(url) {
    var link = document.createElement('a');
    link.href = url;
    return link.hostname;
  }

});


