var AutoCompleteBox = require('../common/autocomplete/auto_complete_box');
var RosterStore = require('../../stores/roster_store');
var Utils = require('../../helpers/utils');
var PresenceIcon = require("../common/icon/presence-icon");

function mentionFilter(mention_text, user){
  var name = user.name.toLowerCase().split(' '),
    partialNameMatch = false;
  for(var i=0;i<name.length;i++){
    if (name[i].indexOf(mention_text) === 0) {
      partialNameMatch = true;
      break;
    }
  }
  return (user.mention_name && user.mention_name.toLowerCase().indexOf(mention_text) === 0) || partialNameMatch ||
    (user.name && user.name.replace(' ','').toLowerCase().indexOf(mention_text) === 0);
}

module.exports = React.createClass({

  displayName: "MentionAutoComplete",

  getDefaultProps: function(){
    return {
      mention_text: '',
      selected_item: 0,
      defaultAtMentions: [
        {
          name: 'Available room members',
          mention_name: 'here',
          presence: {
            show: 'chat'
          }
        },
        {
          name: 'All room members',
          mention_name: 'all',
          presence: {
            show: 'chat'
          }
        },
        {
          name: 'HipChat',
          mention_name: 'HipChat',
          presence: {
            show: 'chat'
          }
        }
      ]
    };
  },

  shouldComponentUpdate: function(nextProps){
    return this.props.mention_text !== nextProps.mention_text
      || this.props.selected_item !== nextProps.selected_item
      || Utils.dot(this.props,'presence.show') !== Utils.dot(nextProps, 'presence.show');
  },

  componentDidUpdate: function(){
    this.props.updateMentionResultsCount(this._resultsCount);
  },

  render: function() {
    var mention_text = this.props.mention_text.toLowerCase(), filtered = [], sortFilter, room, defaultAtMentions;
    defaultAtMentions = this.props.defaultAtMentions;
    sortFilter = function(i, opts){
      // 1st order sort on default @-mentions
      var prefix1 = opts.prioritizeDefault ? _.find(defaultAtMentions, {'name': i.name}) ? '0': '1' : '';
      // 2nd order sort on room participants
      var prefix2 = _.find(room.participants.members.concat(room.participants.guests), {'jid': i.jid}) ? '0': '1';
      // 3rd order sort on start of name match
      var prefix3 = i.name.toLowerCase().indexOf(mention_text) === 0 ? '0' : '1';
      return prefix1 + prefix2 + prefix3 + i.name.toLowerCase();
    };
    if (mention_text === '@') {
      room = RosterStore.getAll();
      filtered = _.sortBy(this.props.defaultAtMentions.concat(room.participants.members).concat(room.participants.guests), x => sortFilter(x, {prioritizeDefault: true}));
    } else if (mention_text !== ''){
      mention_text = mention_text.slice(1);
      room = RosterStore.getAll();
      if (room.active_chat_privacy === 'public') {
        filtered = _.sortBy(
          _.filter(_.toArray(room.users).concat(this.props.defaultAtMentions), mentionFilter.bind(null, mention_text)),
          sortFilter
        );
      } else {
        filtered = _.sortBy(
          _.filter(room.participants.members.concat(this.props.defaultAtMentions), mentionFilter.bind(null, mention_text)),
          sortFilter
        );
      }
    }
    this._resultsCount = filtered.length;
    return (
      <div className="hc-mention-ac hc-chat-input-autocomplete">
        <AutoCompleteBox key="mention_auto_complete" list={filtered}
          textarea_ref={this.props.textarea_ref}
          getItemContent={this._getItemContent}
          selected_item={this.props.selected_item}
          onItemSelected={this.props.onMentionSelected}
          ref="acBox" />
      </div>
    );
  },

  _getItemContent: function(user){
    return (
      <div className="hc-ac-row">
        <div className="hc-ac-preview">
          <PresenceIcon presence={user.presence.show} active="false" />
        </div>
        <span className="hc-ac-name">{user.name}</span>
        <span className="hc-ac-mention-name">@{user.mention_name}</span>
      </div>
    );
  }

});



