var Utils = require('../helpers/utils'),
    moment = require('moment'),
    Store = require('../../lib/core/store'),
    PreferencesStore = require("./preferences_store"),
    ClientPreferencesStore = require("./client_preferences_store"),
    AppDispatcher = require("../dispatchers/app_dispatcher"),
    HCApiDispatcher = require("../dispatchers/HCAPI_dispatcher"),
    AnalyticsDispatcher = require("../dispatchers/analytics_dispatcher"),
    appConfig = require('../config/app_config'),
    Promise = require('es6-promise').Promise;

class ChatWindowStore extends Store {
  constructor() {

    this.local = {
      should_queue_changes: {},
      queued_messages: {},
      initial_join_presences: {},
      echoed_chats: {},
      replacement_messages: {}
    };

    this.defaults = {
      chat: {
        name: '',
        jid: '',
        messages: {},
        ordered_messages: {},
        oldest_timestamp: '',
        newest_timestamp: Utils.format_time_for_history(moment().unix()),
        oldest_mid: '',
        newest_mid: '',
        last_viewed_mid: '',
        scrollTop: '',
        isScrolledToBottom: true,
        active: false,
        fully_initialized: false,
        fetching_recent: false,
        fetching_ancient: false,
        ancient_history_is_empty: false,
        has_no_message_history: false,
        should_scroll_to_bottom: true,
        ancient_msgs: {},
        recent_msgs: {},
        is_composing: false,
        last_active: false
      }
    };

    this.data = {
      current_user: {},
      chats: {},
      active_chat: false,
      mentionRegex: null,
      emoticons: {},
      preferences: {},
      initialized: false,
      chat_show_sidebar: ClientPreferencesStore.shouldShowChatSidebar(),
      groupchat_show_sidebar: ClientPreferencesStore.shouldShowGroupChatSidebar()
    };

    this.registerListeners();
  }

  registerListeners() {
    AppDispatcher.registerOnce({
      'hc-init': () => {
        this.set('active_chat', PreferencesStore.getChatToFocus());
      }
    });
    AppDispatcher.register({
      'updated:emoticons': (emoticons) => {
        this.updateEmoticons(emoticons);
      },
      'updated:current_user': (user) => {
        this.data.current_user = user;
      },
      'updated:mentionRegex': (regex) => {
        this.data.mentionRegex = regex;
      },
      'updated:activeRooms': (rooms) => {
        this.updateRooms(rooms);
      },
      'updated:newMessages': (messages) => {
        this.updateChatMessages(messages);
      },
      'updated:preferences': (prefs) => {
        this.set({
          active_chat: prefs.chatToFocus,
          preferences: prefs
        });
      },
      'before:select-room': (data) => {
        if (!data.jid != this.data.active_chat && this.data.chats[this.data.active_chat]) {
          this.flushQueue(this.data.active_chat);
          this.updateLastViewedMid(this.data.active_chat);
          this.data.chats[this.data.active_chat].last_active = new Date();
        }
      },
      'select-room': (data) => {
        if (data.jid != 'lobby' && !/^lobby@/.test(data.jid)) {
          this.handleRoomSelect(data.jid);
        }
      },
      'send-message': (data) => {
        this.data.chats[data.jid].should_scroll_to_bottom = true;
        if (!this.data.chats[data.jid].fetching_ancient) {
          this.flushQueue(data.jid);
        }
      },
      'resend-message': (msg) => {
        msg.status = 'unconfirmed';
        this.updateChats(msg.room);
        this.scheduleConfirmationFailed(msg);
      },
      'app-state-ready': () => {
        if (this.data.active_chat
          && this.data.chats[this.data.active_chat]
          && Utils.jid.is_private_chat(this.data.active_chat)) {
          this.fetchXMPPHistory(this.data.active_chat);
        }
        this.set('initialized', true);
      },
      'initial-join-presence-sent': (data) => {
        if (this.data.chats[data.jid] && data.with_history) {
          this.initialJoinWithHistory(data.jid);
        }
      },
      'room-joined': (data) => {
        var jid = Utils.jid.bare_jid(data.from);
        if (!this.local.initial_join_presences[jid]) {
          this.local.initial_join_presences[jid] = 0;
        }
        this.local.initial_join_presences[jid]++;
        if (this.local.initial_join_presences[jid] !== 0 && this.local.initial_join_presences[jid] % 2 === 0) {
          if (this.data.chats[jid] && this.data.chats[jid].fetching_ancient) {
            this.handleAncientHistoryLoaded({jid: jid});
          }
          this.local.initial_join_presences[jid] = 0;
          AppDispatcher.dispatch("show-join-messages", {jid: jid});
        }
      },
      'open-room': (data) => {
        this.data.active_chat = data.jid;
        this.handleRoomSelect(this.data.active_chat);
      },
      'set-scroll-value': (data) => {
        _.assign(this.data.chats[data.jid], {
          scrollTop: data.scrollTop,
          isScrolledToBottom: data.isScrolledToBottom,
          should_scroll_to_bottom: data.should_scroll_to_bottom
        });
      },
      'chat-is-scrolling': (data) => {
        this.local.should_queue_changes[data.jid] = data.scrolling;
        if (!data.scrolling) {
          this.flushQueue(data.jid);
        }
      },
      'requesting-ancient-history': (data) => {
        this.fetchXMPPHistory(data.jid);
      },
      'history-loaded': (data) => {
        this.handleAncientHistoryLoaded(data);
      },
      'reconnect-success': () => {
        AppDispatcher.dispatch('hide-join-messages');
        _.forEach(_.filter(this.data.chats, 'fully_initialized'), (room) => {
          this.fetchAPIHistory(room.jid);
        });
      },
      'show-empty-state': (data) => {
        this.data.chats[data.jid].ancient_history_is_empty = true;
        this.set('chats', this.data.chats);
      },
      'status-message-received': (data) => {
        this.handleUserStateMessage(data);
      },
      'save-client-preferences': () => {
        this.set({
          chat_show_sidebar: ClientPreferencesStore.shouldShowChatSidebar(),
          groupchat_show_sidebar: ClientPreferencesStore.shouldShowGroupChatSidebar()
        });
      },
      'replacement-message-received': (data) => {
        this.local.replacement_messages[data.jid] ? this.local.replacement_messages[data.jid].push(data) : this.local.replacement_messages[data.jid] = [data];
      }
    });

    HCApiDispatcher.register({
      'before:API:fetched-recent-history': (data) => {
        if (this.data.chats[data.jid]) {
          this.data.chats[data.jid].fetching_recent = false;
          // Blow away all messages if the fetched messages begin with a mid that we haven't received yet
          if (data.mid && this.data.chats[data.jid].newest_mid !== data.mid) {
            this.data.chats[data.jid].ordered_messages = {};
          }
        }
      },
      'API:fetched-recent-history': (data) => {
        this.data.chats[data.jid].populated_recent = true;
        this.checkIfEmpty(data.jid);
      }
    });
  }

  updateEmoticons(emoticons) {
    Utils.emoticons.path_prefix = emoticons.path_prefix;
    this.data.emoticons = emoticons;
  }

  updateRooms(rooms) {
    this.data.rooms = rooms;
    var hasChanged,
        jids = _.keys(rooms),
        deleted = _.difference(_.keys(this.data.rooms), jids);
    if (deleted.length) {
      this.data.chats = _.omit(this.data.chats, deleted);
      hasChanged = true;
    } else {
      _.forEach(jids, (jid) => {
        if (this.data.chats[jid]) {
          // Update Room Name
          if (this.data.chats[jid].name !== rooms[jid].name) {
            this.data.chats[jid].name = rooms[jid].name;
            hasChanged = true;
          }
        } else {
          this.data.chats[jid] = _.assign({}, _.cloneDeep(this.defaults.chat), {
            name: rooms[jid].name,
            jid: jid,
            active: jid == this.data.active_chat,
            last_active: new Date()
          });
          hasChanged = true;
        }
      });
    }
    if (hasChanged) this.set('chats', this.data.chats);
  }

  handleRoomSelect(jid) {
    if (!this.data.chats[jid].fully_initialized) {
      this.fetchXMPPHistory(jid);
    }
    this.updateMessageBounds(this.data.active_chat);
    this.data.active_chat = jid;
    this.checkIfEmpty(jid);
    this.updateChats(jid);
  }

  updateMessageBounds(jid) {
    var chat = this.data.chats[jid];
    if (_.isEmpty(this.data.chats[jid].messages)) {
      return;
    }
    var sorted = _(this.data.chats[jid].messages)
      .reject((obj) => {
        return obj.is_presence_message;
      })
      .sortBy((msg) => {
        return parseFloat(msg.time);
      }).value();
    if (sorted.length) {
      var first = _.first(sorted),
          last = _.last(sorted);
      _.assign(chat, {
        newest_timestamp: Utils.format_time_for_history(last.time),
        newest_mid: last.mid.split('-link')[0],
        oldest_timestamp: Utils.format_time_for_history(first.time),
        oldest_mid: first.mid.split('-link')[0]
      });
    }
  }

  getMessageCountToPreserve(jid, sortedMessages) {
    var chat = this.getChat(jid);
    var unreadMessagesCount = chat.unreadCount || 0;
    if (chat.isScrolledToBottom) {
      return appConfig.chat_room_trim_buffer + unreadMessagesCount;
    } else {
      var numberToPreserve = _.last(sortedMessages, message => message.mid !== chat.oldestVisibleMessageMid).length + 1;
      return appConfig.chat_room_trim_buffer + unreadMessagesCount + numberToPreserve;
    }
  }

  trimMessages(jid) {
    var chat = this.getChat(jid);
    var messages = chat.messages;
    if (this.chatIsBackgrounded(jid)) {
      var sortedMessages = this.sortMessages(messages);
      var messageCount = sortedMessages.length;
      var numMessagesToPreserve = this.getMessageCountToPreserve(jid, sortedMessages);

      if (messageCount > numMessagesToPreserve) {
        var numMessagesToDelete = messageCount - numMessagesToPreserve;
        var messageMidsToDelete = _.map(_.first(sortedMessages, numMessagesToDelete), message => message.mid);
        messages = _.omit(messages, messageMidsToDelete);
      }
    }

    return messages;
  }

  chatIsBackgrounded(jid) {
    var chat = this.getChat(jid);
    var ninetyMinutesAgo = moment().subtract(appConfig.chat_room_idle_timeout_minutes, 'minutes');
    return chat && chat.jid !== this.data.active_chat && chat.last_active !== false && moment(chat.last_active).isBefore(ninetyMinutesAgo);
  }

  getChat(jid) {
    return this.data.chats[jid];
  }

  updateLastViewedMid(jid) {
    this.data.chats[jid].last_viewed_mid = this.data.chats[jid].newest_mid;
  }

  updateChatMessages(messages) {
    var chat;
    _.forEach(messages, (messages, jid) => {
      chat = this.data.chats[jid];
      if (chat) {
        var chat_is_backgrounded_and_empty = (jid != this.data.active_chat && _.isEmpty(chat.messages));
        if (chat.fetching_ancient || chat_is_backgrounded_and_empty) {
          _.merge(chat.ancient_msgs, messages);
          if (chat_is_backgrounded_and_empty && !HC.isEmbeddedComponent) this.fetchXMPPHistory(jid);
        }
        this.addMessages(jid, _.indexBy(messages, 'mid'));
      }
    });
  }

  scheduleConfirmationFailed(message) {
    if (message.status !== 'unconfirmed') return;
    message.timer = setTimeout(() => {
      var msg = this.data.chats[message.room].messages[message.mid];
      if (msg) {
        msg.status = 'failed';
        this.updateChats(msg.room);
      }
    }, appConfig.message_confirmation_timeout)
  }

  cancelConfirmationFailed(timerid) {
    clearTimeout(timerid);
  }

  addMessages(jid, messages) {
    if (!_.isEmpty(messages)) {
      if (this.local.should_queue_changes[jid]) {
        if (!this.local.queued_messages[jid]) this.local.queued_messages[jid] = {};
        _.merge(this.local.queued_messages[jid], messages);
      } else {
        _.forEach(messages, this.scheduleConfirmationFailed.bind(this));
        this.filterMessages(jid, messages);
        _.merge(this.data.chats[jid].messages, messages);
        this.data.chats[jid].messages = this.trimMessages(jid);
        this.data.chats[jid].messages = this.replaceMessageContent(jid);
        this.data.chats[jid].ordered_messages = this.sortMergeAndGroupMessages(this.data.chats[jid].messages);
        this.updateMessageBounds(jid);
        if (this.data.chats[jid].fully_initialized) this.updateChats(jid);
      }
    }
  }

  flushQueue(jid) {
    this.local.should_queue_changes[jid] = false;
    this.addMessages(jid, this.local.queued_messages[jid]);
    this.local.queued_messages[jid] = {};
  }

  shouldGroupMessages(mostRecent, message) {
    var nonGroupingTypes = [
      'notification',
      'video',
      'twitter_user',
      'twitter_status',
      'file',
      'info'
    ];
    return (mostRecent.sender === message.sender
            && message.time - mostRecent.time <= 300
            && mostRecent.color === message.color
            && !~nonGroupingTypes.indexOf(message.type)
            && !~nonGroupingTypes.indexOf(mostRecent.type));
  }

  updateChats(jid) {
    if (this.data.chats[jid]) {
      this.set('chats', this.data.chats);
    }
  }

  handleUserStateMessage(data) {
    var jid = Utils.jid.bare_jid(data.message.from),
        chat = this.data.chats[jid],
        user = Utils.jid.bare_jid(Utils.dot(data, 'message.delay.from_jid')) || Utils.jid.bare_jid(Utils.dot(data, 'message.from'));
    if (chat && user) {
      switch (data.type) {
        case 'active':
          if (user != this.data.current_user.user_jid) {
            this.removeChatStateMessage(jid);
            clearTimeout(this.local.composing_timeout);
          }
          break;
        case 'composing':
          if (user != this.data.current_user.user_jid) {
            var last_received = chat.messages[chat.newest_mid];
            if (!last_received || data.message.ts - last_received.ts > 1) { //don't show composing message if it's been received less than a second from the last message
              this.data.chats[jid].is_composing = true;
              if (jid === this.data.active_chat) {
                clearTimeout(this.local.composing_timeout);
                this.local.composing_timeout = setTimeout(() => {
                  this.removeChatStateMessage(jid);
                }, appConfig.composing_message_linger_timeout);
              }
            }
          }
          break;
        default:
          break;
      }
      this.updateChats(jid);
    }
  }

  fetchAPIHistory(jid) {
    if (this.data.chats[jid]) {
      this.data.chats[jid].fetching = true;
      HCApiDispatcher.dispatch('API:fetch-recent-history', {
        params: {
          'not-before': this.data.chats[jid].newest_mid || '',
          'max-results': 75
        },
        path: {
          'identifier': /@chat/.test(jid) ? jid.split('@')[0].split('_')[1] : this.data.chats[jid].name,
          'type': /@chat/.test(jid) ? 'user' : 'room'
        },
        jid: jid
      });
    }
  }

  initialJoinWithHistory(jid) {
    this.data.chats[jid].fetching_ancient = true;
    this.local.should_queue_changes[jid] = true;
  }

  fetchXMPPHistory(jid) {
    AnalyticsDispatcher.dispatch('analytics-request-history', {jid: jid});
    if (!this.data.chats[jid].fetching_ancient) {
      this.data.chats[jid].fetching_ancient = true;
      this.local.should_queue_changes[jid] = true;
      AppDispatcher.dispatch('request-ancient-history', {
          jid: jid,
          oldest: this.data.chats[jid].oldest_timestamp
        }
      );
    }
  }

  handleAncientHistoryLoaded(data) {
    var chat = this.data.chats[data.jid];
    chat.fetching_ancient = false;
    var queueSize = 0;
    this.flushQueue(data.jid);
    if (_.isEmpty(this.data.chats[data.jid].ancient_msgs)) {
      this.data.chats[data.jid].ancient_history_is_empty = true;
    } else {
      queueSize = Object.keys(this.data.chats[data.jid].ancient_msgs).length;
      this.data.chats[data.jid].ancient_msgs = {};
    }
    if (!this.data.chats[data.jid].fully_initialized) {
      this.data.chats[data.jid].fully_initialized = true;
      AppDispatcher.dispatch('chat-fully-initialized');
    }
    this.checkIfEmpty(data.jid);
    this.updateChats(data.jid);
    AnalyticsDispatcher.dispatch('analytics-history-loaded', {jid: data.jid, size: queueSize});
  }

  checkIfEmpty(jid) {
    var chat = this.data.chats[jid];
    if (chat
      && !chat.fetching_ancient
      && !chat.fetching_recent
      && _.isEmpty(chat.messages)
      && _.isEmpty(chat.ordered_messages)
      && (!this.local.queued_messages[jid] || _.isEmpty(this.local.queued_messages[jid]))) {
      chat.has_no_message_history = true;
      if (!chat.fully_initialized) {
        chat.fully_initialized = true;
      }
    }
  }

  filterMessages(jid, messages) {
    _.forEach(messages, (msg) => {
      if (this.data.chats[jid].messages[msg.id]) { // messages is echo of previously sent message
        this._markEchoedMessage(jid, msg);
        delete this.data.chats[jid].messages[msg.id];
      } else if (appConfig.slash_replacement_regex.test(msg.body) && msg.id && !msg.is_history_message) {
        var replacement = _.findWhere(this.local.replacement_messages[jid], {mid: msg.mid});
        _.pull(this.local.replacement_messages[jid], replacement);  // delete echoed replacement message
        delete messages[msg.mid];
      } else if (messages[msg.id]) { // echo exists in current messages set
        this._markEchoedMessage(jid, msg);
        delete messages[msg.id];
      } else if (this._wasMessagesAlreadyEchoed(jid, msg)) { // echo has already been received
        this._markEchoedMessage(jid, msg);
        delete messages[msg.mid];
      } else if (this.data.chats[jid].jid === Utils.jid.bare_jid(msg.from)) {
        this.data.chats[jid].is_composing = false;
        clearTimeout(this.local.composing_timeout);
      }
    });
  }

  _wasMessagesAlreadyEchoed(jid, msg) {
    return this.local.echoed_chats[jid] && this.local.echoed_chats[jid].messages[msg.id];
  }

  _markEchoedMessage (jid, msg) {
    this.cancelConfirmationFailed(msg.timer);
    msg.is_echo = true;
    if (!this.local.echoed_chats[jid]) {
      this.local.echoed_chats[jid] = {messages: {}};
    }
    this.local.echoed_chats[jid].messages[msg.id] = msg;
  }

  sortMessages(messages) {
    var person;
    return _.sortBy(messages, (msg) => {
      return msg.time;
    });
  }

  sortMergeAndGroupMessages(unsortedMessages) {
    var grouped = _.groupBy(this.sortMessages(unsortedMessages), msg => Utils.format_time_for_separator(msg.time));
    return _.forOwn(grouped, (messages, date) => {
      var i = 0;
      grouped[date] = _.reduce(this.sortMessages(messages), (result, message) => {
        if (i > 0 && this.shouldGroupMessages(result[i - 1], message)) {
          result[i - 1].messages.push(message);
          return result;
        }
        result[i] = _.assign({
          key: message.mid,
          messages: [message]
        }, message);
        i++;
        return result;
      }, [
        {messages: []}
      ]);
    });
  }

  removeChatStateMessage(jid) {
    if (jid === this.data.active_chat) {
      this.smoothRemoveChatStateMessage(jid);
    } else {
      this.data.chats[jid].is_composing = false;
    }
  }

  smoothRemoveChatStateMessage(jid) {
    this.local.should_queue_changes[jid] = true;
    var promise = new Promise((res) => {
      setTimeout(() => {
        res(jid);
      }, appConfig.chat_scroll_duration);
      AppDispatcher.dispatch('remove-chat-state-message', {jid: jid});
    });
    promise.then((jid) => {
      this.data.chats[jid].is_composing = false;
      this.flushQueue(jid);
      this.updateChats(jid);
    });

  }

  replaceMessageContent(jid) {
    var messages = this.data.chats[jid].messages;
    if (this.local.replacement_messages[jid] && this.local.replacement_messages[jid].length) {
      messages = _.omit(messages, _.pluck(this.local.replacement_messages[jid], 'mid'));
      var ordered = _.sortBy(messages, msg => msg.time),
          lastBy;
      _(this.local.replacement_messages[jid])
        .forEach((msg) => {
          lastBy = _.last(_(ordered).reject((entry) => {
            return entry.time > msg.time || entry.sender_id != msg.sender_id;
          }).value());
          if (lastBy) {
            lastBy.body = lastBy.body.replace(msg.text_to_replace, msg.replacement_text);
            messages[lastBy.mid] = Utils.formatMessageBody(lastBy);
          }
        }, this);
      this.local.replacement_messages[jid] = [];
    }
    return messages;
  }

}

module.exports = new ChatWindowStore();


