var Person = require('./person'),
    RosterEmptyState = require('./roster_empty_state'),
    strings = require('../../strings/roster_panel_strings'),
    Utils = require("../../helpers/utils"),
    RosterStore = require('../../stores/roster_store'),
    RightPanelActions = require('../../actions/right_panel_actions');

module.exports = React.createClass({

  displayName: "RightSideBarRoster",

  getInitialState: function() {
    return {
      selectedPerson: false
    }
  },

  componentDidMount: function() {
    var size = 0;

    var members = Utils.dot(this.props, "participants.members");
    if (members){
      size =  members.length;
    }

    var id = RosterStore.get("active_chat");
    RightPanelActions.rosterMounted({id: id, size: size});
  },

  _onClick: function(jid) {
    this.setState({
      selectedPerson: jid
    });
  },

  _showEmptyState: function () {
    var current_user_jid = this.props.current_user_jid,
        members = this.props.participants['members'],
        guests = this.props.participants['guests'],
        result;

    if (members.length === 1 && !guests.length) {
      result = _.findWhere(members, {jid: current_user_jid});
    } else if (guests.length === 1 && !members.length) {
      result = _.findWhere(guests, {jid: current_user_jid});
    }
    return (result) ? true : false;
  },

  _isAdmin: function (user) {
    return Utils.user.is_admin(this.props.admins, false, user);
  },

  _hasMembersAndGuests: function () {
    return this.props.participants['members'] && this.props.participants['members'].length && this.props.participants['guests'] && this.props.participants['guests'].length
  },

  render: function(){
    var cx = React.addons.classSet,
        rosterClasses = cx({
          'hc-roster': true,
          'hc-sidebar-scroll': true
        }),
        guest_url_message;

    if (this.props.guest_url) {
      guest_url_message = (
        <div className="guest-access">
          <strong>{strings.guest_access_enabled}</strong>
          <br />
          <a href={this.props.guest_url} target="_blank">{this.props.guest_url}</a>
        </div>
      );
    }

    return (
      <div className={(this.props.guest_url ? "guest-access-enabled " : "") + "roster-wrap"}>
        <div className={rosterClasses}>
          <div id="hc-status-tooltip"></div>
          {
            _.map(['members', 'guests'], function(groupName) {
              return (
                this.props.participants[groupName] && this.props.participants[groupName].length ?
                <ul className="aui-nav aui-navgroup-vertical" key={"RosterItem" + groupName}>
                  {this._hasMembersAndGuests() ?
                    <li className="aui-nav-heading">
                      <strong className="uppercase">{strings[groupName]}</strong>
                    </li> :
                    <div></div>}
                  {
                    _.map(this.props.participants[groupName], function (user, jid) {
                      return <Person key={jid} user={user} onChatOpen={this.props.onChatOpen} active={this.state.selectedPerson === user.jid} isAdmin={this._isAdmin(user)} onPersonClick={this._onClick}/>;
                    }, this)
                  }
                </ul> : _.noop(groupName)
              );
            }, this)
          }
          {this._showEmptyState() ?
            <RosterEmptyState room_privacy={this.props.active_chat_privacy} users={this.props.users} isAdmin={this._isAdmin(this.props.users[this.props.current_user_jid])}/> : ""
          }
        </div>
        {guest_url_message}
      </div>
    );
  }
});



