var InfiniteScrollParentMixin = require('../mixins/infinite_scroll_parent_mixin'),
    LobbyListing = require('./lobby_listing'),
    lobbyStore = require('../../stores/lobby_store'),
    LobbyActions = require('../../actions/lobby_actions'),
    strings = require('../../strings/common_strings'),
    AppDispatcher = require('../../dispatchers/app_dispatcher');

var debouncedFilter = _.debounce(LobbyActions.applyFilter, 200);

module.exports = React.createClass({

  displayName: "Lobby",

  mixins: [InfiniteScrollParentMixin],

  getInitialState: function () {
    return this._getDefaultState(this._getState());
  },

  componentDidMount: function () {
    lobbyStore.on('change', this._onChange);
    AppDispatcher.register('select-room', this._roomSelected);
    LobbyActions.lobbyMounted();
    this._focusAndSelect();
  },

  componentWillUnmount: function () {
    lobbyStore.off('change', this._onChange);
    AppDispatcher.unregister('select-room', this._roomSelected);
  },

  render: function () {
    return (
      <div className="hc-rooms-container">
        <header className="aui-page-header lobby-header">
            <div className="aui-page-header-inner">
                <div className="aui-page-header-main hc-lobby-search-content">
                  <form className="aui hc-filter-form" onSubmit={this._onSubmit}>
                    <label className="assistive" htmlFor="rosterfilter">{strings.filter}</label>
                    <input ref="lobbySearchInput" type="text" placeholder={strings.filter} className="text hc-filter long-field mousetrap" onChange={this._onSearchFilter} onKeyPress={this._onKeyPress} value={this.state.filter.query}/>
                  </form>
                </div>
                <div className="aui-page-header-actions">
                    <div className="aui-buttons">
                        <button className={"aui-button aui-button-light " + (this.state.filter.scope === "all" ? "active" : "")} onClick={this._onFilter.bind(null,'all')}>{strings.all}</button>
                        <button className={"aui-button aui-button-light " + (this.state.filter.scope === "rooms" ? "active" : "")} onClick={this._onFilter.bind(null,'rooms')}>{strings.rooms}</button>
                        <button className={"aui-button aui-button-light " + (this.state.filter.scope === "people" ? "active" : "")} onClick={this._onFilter.bind(null,'people')}>{strings.people}</button>
                    </div>
                    <div className="aui-buttons">
                        <button onClick={this._onCreateRoom} className="aui-button aui-button-light" id="create-room-button">{strings.buttons.create_room}</button>
                    </div>
                </div>
            </div>
        </header>
        <div className="hc-lobby-panel" ref="scrollable" onScroll={this._onScroll}>
          <div className="aui-page-panel-inner">
            <section className="aui-page-panel-content hc-lobby-panel-content">
              <LobbyListing rows={this.state.rows}
                            height={this.state.height}
                            onChatOpen={this._onChatOpen}
                            total={this.state.total}
                            visibleStart={this.state.visibleStart}
                            visibleEnd={this.state.visibleEnd}
                            displayStart={this.state.displayStart}
                            displayEnd={this.state.displayEnd}
                            rowHeight={this.state.rowHeight}/>
            </section>
          </div>
        </div>
      </div>
    );
  },

  _getState: function () {
    var rows;
    var filter = lobbyStore.get('filter');
    if (filter.scope === 'all' && filter.query === '') {
      rows = lobbyStore.get('all');
    } else {
      rows = lobbyStore.get('filtered');
    }
    return {
      rows: rows,
      total: _.size(rows),
      filter: filter,
      height: 1000
    }
  },

  _onKeyPress: function(e) {
    if (e.which === 13) {
      var firstItem = _.first(_.keys(this.state.rows));
      if (firstItem) {
        e.preventDefault();
        LobbyActions.openChat(this.state.rows[firstItem]);
      }
    }
  },

  _onChange: function () {
    this.setState(this._getState(), function () {
      this._scrollState(0);
      this.forceUpdate();
    });
  },

  _onFilter: function (scope) {
    LobbyActions.applyFilter({
      scope: scope
    });
    this.refs.scrollable.getDOMNode().scrollTop = 0;
  },

  _onSearchFilter: function (input) {
    debouncedFilter({
      query: input.target.value
    });

    // This is necessary since we don't want the UI debounced
    this.setState({
      filter: {
        query: input.target.value
      }
    });
    this.refs.scrollable.getDOMNode().scrollTop = 0;
  },

  _onCreateRoom: function (e) {
    e.preventDefault();
    LobbyActions.showCreateRoomDialog();
  },

  _onChatOpen: LobbyActions.openChat,

  _onSubmit: function (e) {
    e.preventDefault();
  },

  _roomSelected: function (data) {
    if (data.jid === 'lobby') {
      this._focusAndSelect();
    }
  },

  _focusAndSelect: function () {
    var input = this.refs.lobbySearchInput.getDOMNode();
    if (input !== document.activeElement) {
      input.focus();
      input.select();
    }
  }
});



