var AppHeaderActions = require('../../actions/app_header_actions');
var AppDispatcher = require('../../dispatchers/app_dispatcher');

module.exports = React.createClass({

  displayName: "SearchInput",

  componentDidMount: function () {
    this._shouldFocus();
  },

  componentDidUpdate: function () {
    this._shouldFocus();
  },

  _shouldFocus: function () {
    if (this.props.focus_search) {
      this._focusAndSelect();
    }
  },

  _focusAndSelect: function () {
    var input = this.refs.searchInput.getDOMNode();
    if (input !== document.activeElement) {
      input.focus();
      input.select();
    }
  },

  render: function() {
    return (
      <div className="aui-quicksearch">
        <input id="search-query" type="text" placeholder="Search history" name="name" autoComplete="off" title="search for something" ref="searchInput"
          onKeyDown={this._onKeyDown}
          onChange={this._onChange}
          onBlur={this._onBlur}
          value={this.props.text}></input>
      </div>
    );
  },

  _onKeyDown: function(evt) {
    if (evt.keyCode === 13) {
      AppHeaderActions.searchHistory(this.props.jid);
    }
  },

  _onChange: function(evt) {
    AppHeaderActions.setSearchText({
      text: evt.target.value
    });
  },

  _onBlur: function() {
    AppHeaderActions.searchBlurred();
  }
});


