module.exports = React.createClass({

  displayName: "NavPanelContent",

  render: function() {
    return (
      <div className="nav-panel-content" aria-hidden={!this.props.selected} aria-labeledby={this.props.tabId}>
        <h2>{this.props.name}</h2>
        {this.props.children}
      </div>
    )
  }
});


