var AuiRadio = require("../../common/aui/form/aui_radio");
var PreferencesStore = require("../../../stores/preferences_store");
var ClientPreferencesStore = require("../../../stores/client_preferences_store");
var PreferencesKeys = require("../../../keys/preferences_keys");
var strings = require("../../../strings/dialog_strings");

module.exports = React.createClass({

  displayName: "PreferenceRadioField",

  _isDefaultChecked: function(val) {
    var isServerPref = _.contains(_.values(PreferencesKeys), this.props.id);
    var store = (isServerPref) ? PreferencesStore : ClientPreferencesStore;
    return val === store.get(this.props.id);
  },

  render: function () {
    return (
      <fieldset className="group">
        <legend>
          <span>{this.props.label}</span>
        </legend>
        {
          _(this.props.options)
            .map((option) => {
              return <AuiRadio
                val={option}
                id={option}
                label={strings[option]}
                name={this.props.id}
                defaultChecked={this._isDefaultChecked(option)}
                onChange={_.noop} />
          }, this)
        }
      </fieldset>

    );
  }
});


