var AppDispatcher = require('../dispatchers/app_dispatcher');
var PreferencesStore = require("./preferences_store");
var ClientPreferencesStore = require("./client_preferences_store");
var ClientPrefKeys = require("../keys/client_preferences_keys");
var Store = require("../../lib/core/store");
var Utils = require('../helpers/utils');

class RosterStore extends Store {

  getDefaults() {
    return {
      admins: [],
      current_user: {},
      users: {},
      rooms: {},
      files: [],
      links: [],
      loading: false,
      participants: {
        members : [],
        guests : []
      },
      initialized: false,
      active_chat: null,
      active_chat_privacy: null,
      guest_url: '',
      chat_type: ''
    };
  }

  registerListeners() {
    AppDispatcher.registerOnce({
      'hc-init': () => {
        this.data.active_chat = PreferencesStore.getChatToFocus();
        this.fetchPanelContent(this.data.active_chat);
      }
    });
    AppDispatcher.register({
      'updated:current_user': (user) => {
        this.data.current_user = user;
      },
      'updated:preferences': (prefs) => {
        this.data.active_chat = prefs.chatToFocus;
      },
      'updated:roster': (roster) => {
        this.handleRosterUpdate(roster);
      },
      'app-state-ready': () => {
        this.data.initialized = true;
      },
      'fetch-files': () => {
        if (this.getActivePanel(this.data.chat_type) == "files") {
          this.set('loading', true);
        }
      },
      'files-fetched': () => {
        this.set('loading', false);
        this.handleSelectRoom(this.data.active_chat);
      },
      'add-file': (data) => {
        if (data.room == this.data.active_chat) {
          this.handleNewFile(data.file);
        }
      },
      'fetch-links': () => {
        if (this.getActivePanel(this.data.chat_type) == "links") {
          this.set('loading', true);
        }
      },
      'links-fetched': () => {
        this.set('loading', false);
        this.handleSelectRoom(this.data.active_chat);
      },
      'add-link': (data) => {
        if (data.room == this.data.active_chat) {
          this.handleNewLink(data.link);
        }
      },
      'new-room-created': (room) => {
        this.handleNewRoom(room);
      },
      'mark-participant-unknown': (data) => {
        this.markParticipantForPrivateRoom(data);
      },
      'unmark-participant': (data) => {
        this.unmarkParticipantForPrivateRoom(data);
      },
      'updated:active_chat': (jid) => {
        this.handleSelectRoom(jid);
        this.fetchPanelContent(jid);
      },
      'updated:activeRooms': (rooms) => {
        this.handleActiveRoomsUpdate(rooms);
      },
      'select-panel': (data) => {
        this.selectPanel(data);
      },
      'open-room': (room) => {
        this.data.active_chat = room.jid;
        this.handleSelectRoom(room.jid);
        this.fetchPanelContent(room.jid);
      }
    });
  }

  saveClientPrefs() {
    AppDispatcher.dispatch("save-client-preferences", ClientPreferencesStore.getAll());
  }

  selectPanel(data) {
    if (this.shouldToggleSidebar(data.activePanel)) {
      this.toggleSidebar(this.data.chat_type);
    }
    this.setPanel(data.activePanel);
    this.fetchPanelContent(this.data.active_chat);
    this.saveClientPrefs();
  }

  setPanel(panel) {
    if (this.data.chat_type === "chat") {
      ClientPreferencesStore.set(ClientPrefKeys.CHAT_ACTIVE_PANEL, panel);
    } else if (this.data.chat_type === "groupchat") {
      ClientPreferencesStore.set(ClientPrefKeys.GROUPCHAT_ACTIVE_PANEL, panel);
    }
  }

  shouldToggleSidebar(panel) {
    var result = false;
    if (this.data.chat_type == "chat") {
      result = (panel == ClientPreferencesStore.getChatActivePanel() || !ClientPreferencesStore.shouldShowChatSidebar());
    } else if (this.data.chat_type == "groupchat") {
      result = (panel == ClientPreferencesStore.getGroupChatActivePanel() || !ClientPreferencesStore.shouldShowGroupChatSidebar());
    }
    return result;
  }

  toggleSidebar(chat_type) {
    if (chat_type == "chat") {
      ClientPreferencesStore.set(ClientPrefKeys.SHOW_CHAT_SIDEBAR, !ClientPreferencesStore.shouldShowChatSidebar());
    } else if (chat_type == "groupchat") {
      ClientPreferencesStore.set(ClientPrefKeys.SHOW_GROUPCHAT_SIDEBAR, !ClientPreferencesStore.shouldShowGroupChatSidebar());
    }
  }

  sidebarShowing(jid) {
    return ((Utils.room.detect_chat_type(jid) == "chat" && ClientPreferencesStore.shouldShowChatSidebar()) || (Utils.room.detect_chat_type(jid) == "groupchat" && ClientPreferencesStore.shouldShowGroupChatSidebar()));
  }

  getActivePanel(chat_type) {
    if (chat_type == "chat") {
      return ClientPreferencesStore.getChatActivePanel();
    } else if (chat_type == "groupchat") {
      return ClientPreferencesStore.getGroupChatActivePanel();
    }
  }

  fetchPanelContent(jid) {
    // Only fetch if sidebar is visible
    if (jid !== "lobby" && this.sidebarShowing(jid)) {
      var event;
      switch (this.getActivePanel(this.data.chat_type)) {
        case 'files':
          event = 'fetch-files';
          break;
        case 'links':
          event = 'fetch-links';
          break;
      }
      if (event) {
        AppDispatcher.dispatch(event, {room: jid});
      }
    }
  }

  markParticipantForPrivateRoom(data) {
    if (this.data.users[data.user]) {
      this.data.users[data.user]['not_participating'] ?
        this.data.users[data.user]['not_participating'].push(data.room) :
        this.data.users[data.user]['not_participating'] = [data.room];
    }
  }

  unmarkParticipantForPrivateRoom(data) {
    if (this.data.users[data.user]
      && this.data.users[data.user]['not_participating']
      && this.data.users[data.user]['not_participating'].indexOf(data.room) != -1) {
      this.data.users[data.user]['not_participating'] = _.without(this.data.users[data.user]['not_participating'], data.room);
    }
  }

  sortRoster(roster) {
    var temp = [], grouped = _.groupBy(roster, function (person) {
      return Utils.dot(person, "presence.show", false);
    });
    _.each(['chat', 'away', 'dnd', 'mobile', 'unavailable', 'unknown'], function(presence) {
      _.each(_.sortBy(grouped[presence], 'name'), function(room){
        temp.push(room);
      });
    });
    return temp;
  }

  handleActiveRoomsUpdate(rooms) {
    var deleted = _.difference(_.keys(this.data.rooms), _.keys(rooms));
    if (deleted.length) {
      this.data.rooms = _.omit(this.data.rooms, deleted);
    } else {
      this.data.rooms = _.merge(this.data.rooms, rooms);
    }
    if (this.data.active_chat && this.data.active_chat != 'lobby') {
      this.data.rooms[this.data.active_chat] = rooms[this.data.active_chat];
      this.handleSelectRoom(this.data.active_chat);
    }
  }

  handleRosterUpdate(roster) {
    _.map(this.data.participants, (group, role) => {
      if (this.data.participants[role].length) this.sortRoster(this.data.participants[role]);
    });
    this.data.users = roster;
    this.handleSelectRoom(this.data.active_chat);
  }

  handleNewRoom(room) {
    this.set({
      active_chat: room.jid,
      chat_type: Utils.room.detect_chat_type(room.jid)
    });
    if (room.privacy === "private") {
      AppDispatcher.dispatch("show-modal-dialog", "invite-users-dialog");
    }
  }

  handleNewLink(link) {
    if (!this.data.links) {
      this.data.links = [];
    }
    this.data.links.unshift(link);
    this.set("links", this.data.links);
  }

  handleNewFile(file) {
    if (!this.data.files) {
      this.data.files = [];
    }
    this.data.files.unshift(file);
    this.set("files", this.data.files);
  }

  handleSelectRoom(jid) {
    if (!jid || jid === "lobby") {
      return;
    }

    var type = Utils.room.detect_chat_type(jid),
        room_privacy,
        files,
        links,
        admins,
        new_data,
        guest_url;

    this.data.participants = {
      members : [],
      guests : []
    };

    if (this.data.rooms[jid]) {
      room_privacy = this.data.rooms[jid].privacy;
      guest_url = this.data.rooms[jid].guest_url;
      files = this.data.rooms[jid].files;
      links = this.data.rooms[jid].links;
      admins = this.data.rooms[jid].admins;
      type = this.data.rooms[jid].type;
      _.map(this.data.rooms[jid].participants, (group, role) => {
        _.each(group, (user_jid) => {
          if (this.data.rooms[jid].privacy === 'private') {
            if (this.data.users[user_jid]
                && this.data.users[user_jid].not_participating
                && this.data.users[user_jid].not_participating.indexOf(jid) != -1) {
              this.data.participants[role].push(_.assign({}, this.data.users[user_jid], {
                presence: {
                  show: 'unknown',
                  status: '',
                  idleTime: false
                }
              }));
            } else {
              this.data.participants[role].push(this.data.users[user_jid]);
            }
          } else {
            this.data.participants[role].push(this.data.users[user_jid]);
          }
        });
        this.data.participants[role] = this.sortRoster(this.data.participants[role]);
      });
    }
    new_data = {
      admins : admins,
      users : this.data.users,
      participants : this.data.participants,
      active_chat : jid,
      active_chat_privacy: room_privacy,
      chat_type : type,
      files : files,
      links : links,
      guest_url: guest_url,
      loading : this.data.loading
    };
    this.set(new_data);
  }

}

module.exports = new RosterStore();



