var ModalDialog = require("../../common/modal_dialog/modal_dialog"),
    NavPanel = require("../../common/nav_panel/nav_panel"),
    NavPanelContent = require("../../common/nav_panel/nav_panel_content"),
    PreferenceCheckboxField = require("./preference_checkbox_field"),
    PreferenceRadioField = require("./preference_radio_field"),
    PreferencesStore = require("../../../stores/preferences_store"),
    ClientPreferencesStore = require("../../../stores/client_preferences_store"),
    PreferencesActions = require("../../../actions/preferences_actions"),
    PreferencesKeys = require("../../../keys/preferences_keys"),
    strings = require('../../../strings/dialog_strings'),
    ClientPreferencesKeys = require("../../../keys/client_preferences_keys");

module.exports = React.createClass({

  displayName: "SettingsDialog",

  componentDidMount: function() {
    PreferencesStore.on("change", this._onChange);
    ClientPreferencesStore.on("change", this._onChange);
  },

  componentWillUnmount: function() {
    PreferencesStore.off("change", this._onChange);
    ClientPreferencesStore.off("change", this._onChange);
  },

  _onChange: function() {
    this.setState(this._getState());
  },

  getInitialState: function() {
    return this._getState();
  },

  _getState: function() {
    var prefs = {};
    var preferences = PreferencesStore.getAll();
    var clientPreferences = ClientPreferencesStore.getAll();

    _.assign(prefs, preferences, clientPreferences);

    return prefs;
  },

  _dialogBody: function() {
    var appearanceSettings;
    if (this.state.features && this.state.features.web_client_appearance_settings) {
      appearanceSettings = <NavPanelContent name="Appearance">
        <form className="aui">
          <PreferenceRadioField id={ClientPreferencesKeys.THEME} label={strings.theme}
                                options={ClientPreferencesStore.getThemeOptions()}/>
          <PreferenceRadioField id={ClientPreferencesKeys.DENSITY} label={strings.density}
                                options={ClientPreferencesStore.getDensityOptions()}/>
          <PreferenceCheckboxField id={ClientPreferencesKeys.SHOW_AVATARS} label={strings.show_avatars} />
        </form>
      </NavPanelContent>
    } else {
      appearanceSettings = <span name="Appearance"></span>;
    }
    return (
      <NavPanel onChange={this._onPreferenceChanged}>
        <NavPanelContent name="Notifications">
          <form className="aui">
            <h3>Notify me when</h3>
            <PreferenceCheckboxField id={PreferencesKeys.NOTIFY_FOR_ROOM} label={strings.notify_for_room} />
            <PreferenceCheckboxField id={PreferencesKeys.NOTIFY_FOR_PRIVATE_ROOM} label={strings.notify_for_private_room} />
            <PreferenceCheckboxField id={PreferencesKeys.NOTIFY_FOR_TAG} label={strings.notify_for_tag} />
            <PreferenceCheckboxField id={PreferencesKeys.NOTIFY_FOR_PRIVATE} label={strings.notify_for_private} />
            <h3>Notify me by</h3>
            <PreferenceCheckboxField id={PreferencesKeys.DISABLE_SOUNDS} label={strings.disable_sounds} value="false" isReverse={true} />
            <PreferenceCheckboxField id={PreferencesKeys.SHOW_TOASTERS} label={strings.show_toasters} />
          </form>
        </NavPanelContent>
        <NavPanelContent name="General settings">
          <form className="aui">
            <PreferenceCheckboxField id={PreferencesKeys.HIDE_PRESENCE_MESSAGES} label={strings.hide_presence_messages} value="false" isReverse={true} />
            <PreferenceCheckboxField id={PreferencesKeys.USE_24_HR_FORMAT} label={strings.use_24_hour_format} />

            <PreferenceCheckboxField id={ClientPreferencesKeys.HIDE_GIFS_BY_DEFAULT} label={strings.hide_gifs_by_default} />
            <PreferenceCheckboxField id={ClientPreferencesKeys.REPLACE_TEXT_EMOTICONS} label={strings.replace_text_emoticons} />
            <PreferenceCheckboxField id={ClientPreferencesKeys.SHOW_UNREAD_DIVIDER} label={strings.show_unread_divider} />
          </form>
        </NavPanelContent>
        {appearanceSettings}
      </NavPanel>
    );
  },

  _getPreference: function(target) {
    var value = (target.value === "true");
    var pref = {};
    pref[target.id] = (target.checked) ? value : !value;

    return pref;
  },

  _getRadioPreference: function(target) {
    var checked = document.querySelector(`input[name="${target.name}"]:checked`),
        pref = {};
    pref[target.name] = checked.id;
    return pref;
  },

  _onPreferenceChanged: function(e) {
    var target = e.target;
    var data = target.type === 'radio' ? this._getRadioPreference(target) : this._getPreference(target);
    var isServerPref = _.contains(_.values(PreferencesKeys), target.id);
    if (isServerPref) {
      PreferencesActions.savePreferences(data);
    } else {
      PreferencesActions.saveClientPreferences(data);
    }
  },

  render: function() {
    return (
      <ModalDialog dialogId="settings-dialog" title="Settings" dialogBody={this._dialogBody} />
    );
  }
});


