var RoomDropDown = require("./room_dropdown"),
    CurrentUserStore = require('../../stores/current_user_store'),
    RosterStore = require("../../stores/roster_store"),
    Utils = require("../../helpers/utils"),
    AppStore = require('../../stores/application_store'),
    ClientPreferencesStore = require("../../stores/client_preferences_store"),
    strings = require('../../strings/chat_header_strings');

module.exports = React.createClass({

  displayName: "RoomActions",

  componentDidMount: function () {
    ClientPreferencesStore.on(['change'], this._onChange);
    CurrentUserStore.on(['change'], this._onChange);
    this._createTooltip();
  },

  componentWillUnmount: function () {
    ClientPreferencesStore.off(['change'], this._onChange);
    CurrentUserStore.off(['change'], this._onChange);
    this._destroyTooltip();
  },

  getInitialState: function () {
    return this._getState();
  },

  _getState: function () {
    return {
      chat_show_sidebar: ClientPreferencesStore.shouldShowChatSidebar(),
      groupchat_show_sidebar: ClientPreferencesStore.shouldShowGroupChatSidebar(),
      current_user: CurrentUserStore.get("user")
    };
  },

  _onChange: function() {
    this.setState(this._getState())
  },

  _isAdmin: function () {
    return Utils.user.is_admin(this.props.info.admins, this.props.info.owner, this.state.current_user);
  },

  render: function () {

    var buttonsMarkup = {

      groupchat :

        <div className="aui-page-header-actions hc-chat-header-actions">
          <div className="aui-buttons">
            <button className={"aui-button aui-button-light " + ((this.props.groupchat_active_panel == 'roster' && this.state.groupchat_show_sidebar) ? 'active' : '')}
                    onClick={this.props.onPanelSelect.bind(null, 'roster')}
                    title={this.state.groupchat_show_sidebar ? (this.props.groupchat_active_panel == 'roster' ? strings.hide_people : strings.people) : strings.show_people}>
              <span className="aui-icon aui-icon-small aui-iconfont-user">{strings.people}</span>
            </button>
            <button className={"aui-button aui-button-light " + ((this.props.groupchat_active_panel == 'files' && this.state.groupchat_show_sidebar) ? 'active' : '')}
                    onClick={this.props.onPanelSelect.bind(null, 'files')}
                    title={this.state.groupchat_show_sidebar ? (this.props.groupchat_active_panel == 'files' ? strings.hide_files : strings.files) : strings.show_files}>
              <span className="aui-icon hipchat-icon-small icon-file">{strings.files}</span>
            </button>
            <button className={"aui-button aui-button-light " + ((this.props.groupchat_active_panel == 'links' && this.state.groupchat_show_sidebar) ? 'active' : '')}
                    onClick={this.props.onPanelSelect.bind(null, 'links')}
                    title={this.state.groupchat_show_sidebar ? (this.props.groupchat_active_panel == 'links' ? strings.hide_links : strings.links) : strings.show_links}>
              <span className="aui-icon hipchat-icon-small icon-link">{strings.links}</span>
            </button>
            <button id="room-actions-btn" className="aui-button aui-button-light aui-dropdown2-trigger aui-dropdown2-trigger-arrowless" aria-owns="room-actions-drop-down" aria-controls="room-actions-drop-down" aria-haspopup="true" title={strings.room_actions}>
              <span className="aui-icon aui-icon-small aui-iconfont-more">{strings.room_actions}</span>
            </button>
          </div>
          <RoomDropDown
            roomId={this.props.info.id}
            triggerId="room-actions-btn"
            isAdmin={this._isAdmin()}
            privacy={this.props.info.privacy}
            room={this.props.info}/>
        </div>,

      chat :

        <div className="aui-page-header-actions hc-chat-header-actions">
          <div className="aui-buttons">
            <button className={"aui-button aui-button-light " + ((this.props.chat_active_panel == 'files' && this.state.chat_show_sidebar) ? 'active' : '')}
                    onClick={this.props.onPanelSelect.bind(null, 'files')}
                    title={this.state.chat_show_sidebar ? (this.props.chat_active_panel == 'files' ? strings.hide_files : strings.files) : strings.show_files}>
              <span className="aui-icon hipchat-icon-small icon-file">{strings.files}</span>
            </button>
            <button className={"aui-button aui-button-light " + ((this.props.chat_active_panel == 'links' && this.state.chat_show_sidebar) ? 'active' : '')}
                    onClick={this.props.onPanelSelect.bind(null, 'links')}
                    title={this.state.chat_show_sidebar ? (this.props.chat_active_panel == 'links' ? strings.hide_links : strings.links) : strings.show_links}>
              <span className="aui-icon hipchat-icon-small icon-link">{strings.links}</span>
            </button>
          </div>
        </div>,

      none:

        <div></div>

    };

    return buttonsMarkup[this.props.type] || buttonsMarkup['none'];

  },

  _createTooltip() {
    AJS.$('.hc-chat-header-actions button').tooltip();
  },

  _destroyTooltip() {
    AJS.$('.hc-chat-header-actions button').off();
  }

});


