var Store = require("../../lib/core/store"),
    Storage = require('../helpers/storage'),
    AppDispatcher = require("../dispatchers/app_dispatcher"),
    Utils = require('../helpers/utils');

class AppHeaderStore extends Store {

  constructor() {

    this.notification_supported = typeof Notification != 'undefined';

    this.data = {
      ready: false,
      active_chat: false,
      search_text: '',
      focus_search: false,
      notification_supported: this.notification_supported,
      notification_dismissed: this.getDismissalCount(),
      notification_dismissed_forever: Storage.get('notificationBannerDismissedForever'),
      notification_preference: false,
      notification_permission: this.getNotificationPermission()
    };
    this.registerListeners();
  }

  registerListeners() {
    AppDispatcher.register({
      'app-state-ready': () => {
        this.set({
          ready: true
        });
      },
      'updated:active_chat': (jid) => {
        this.set({
          active_chat: jid
        });
      },
      'updated:preferences': (data) => {
        this.set({
          notification_preference : data.showToasters,
          notification_dismissed : this.getDismissalCount()
        });
      },
      'search-history': (data) => {
        this.handleSearch(data);
      },
      'set-search-text': (data) => {
        this.set('search_text', data.text);
      },
      'request-notification-permission': () => {
        Notification.requestPermission();
      },
      'dismiss-notification-banner': () => {
        Storage.set('notificationBannerDismissalCount', this.getDismissalCount() + 1 || 1);
      },
      'dismiss-notification-banner-forever': () => {
        Storage.set('notificationBannerDismissedForever', true);
      },
      'focus-search': () => {
        this.set('focus_search', true);
      },
      'blur-search': () => {
        this.set('focus_search', false);
      }
    });
  }

  handleSearch(data) {
    var room_name = Utils.jid.room_name(data.jid),
        url = '/search?q=' + _.escape(this.data.search_text) + '&r=' + _.escape(room_name);
    window.open(url);
    this.set('search_text', '');
  }

  getDismissalCount() {
    return Storage.get('notificationBannerDismissalCount');
  }

  getNotificationPermission() {
    if (this.notification_supported) {
      return Notification.permission !== 'default';
    }
  }
}

module.exports = new AppHeaderStore();


