var InfiniteScrollChildMixin = require('../mixins/infinite_scroll_child_mixin');
var LobbyItem = require('./lobby_item');

module.exports = React.createClass({

  displayName: "LobbyListing",

  mixins: [InfiniteScrollChildMixin],
  render: function(){
    var itemNodes = {};
    var items = _.map(this.props.rows);
    for (var i = this.props.displayStart; i <= this.props.displayEnd; ++i) {
      var item = items[i];
      if (item) {
        itemNodes['row-' + i] = (
          <LobbyItem item={item} key={item.jid} onChatOpen={this.props.onChatOpen}/>
        );
      }
    }
    return (
      <table className="aui hc-lobby-list-table">
        <tbody>
          <tr className="hc-lobby-row-scroll-marker hc-lobby-row-top" style={{height: this.props.displayStart * this.props.rowHeight}}>
          </tr>
          {itemNodes}
          <tr className="hc-lobby-row-scroll-marker hc-lobby-row-bottom" style={{height: ((_.size(this.props.rows) - this.props.displayEnd) * this.props.rowHeight) - this.props.rowHeight}}>
          </tr>
        </tbody>
      </table>
    );
  }
});



