var Utils = require('../../../helpers/utils');
var Expando = require('../expando');
var FailedMessage = require('../failed_message');
var cx = React.addons.classSet;

module.exports = React.createClass({

  displayName: "MessageMessageType",

  getInitialState: function () {
    return {
      truncated: true
    };
  },

  componentDidMount: function() {
    this._replaceImageWithRetina(this.getDOMNode());
  },

  _getRenderedMessage: function (msg) {
    if (msg.format == 'code') {
      return this._codify(msg.rendered_body);
    } else if (msg.format == 'monospace') {
      return (
        <Expando setScroll={this.props._setScroll}>
          <pre><div data-mid={msg.mid} />{msg.body}</pre>
        </Expando>
      );
    }
    return (msg.format == 'html') ?
      <div data-mid={msg.mid} dangerouslySetInnerHTML={{__html: msg.body}} />
      :
      this._truncateLongText(msg.body, msg.rendered_body, msg.mid);
  },

  render: function () {
    var msg = this.props.msg;
    var renderedMessage = this._getRenderedMessage(msg);
    return msg.status === 'failed' ? (<FailedMessage msg={msg}>{renderedMessage}</FailedMessage>) : renderedMessage;
  },

  _truncateLongText: function (body, rendered_body, mid) {
    var matches = body.match(/<br \/>/);
    if (!matches || matches.length == 0) {
      matches = body.match(/[\n\r\u2028]+.+/g);
    }
    var num_lines = (matches ? matches.length + 1 : 1);
    if (num_lines < Utils.TRUNCATE_LINES && body.length < Utils.TRUNCATE_CHARS) {
      return <div data-mid={mid} className="msg-line" dangerouslySetInnerHTML={{__html: rendered_body}} />;
    }
    return (
      <Expando setScroll={this.props._setScroll}>
        <div className="msg-wrap" dangerouslySetInnerHTML={{__html: _.escape(body)}} />
      </Expando>
    );
  },

  _codify: function (body) {
    var matches = body.match(/<br \/>/);
    if (!matches || matches.length == 0) {
      matches = body.match(/[\n\r\u2028]+.+/g);
    }
    var num_lines = (matches ? matches.length + 1 : 1);
    if (num_lines < Utils.TRUNCATE_LINES && body.length < Utils.TRUNCATE_CHARS) {
      return <div className="msg-line">
        <div className="pre-wrap" dangerouslySetInnerHTML={{__html: prettyPrintOne('<pre class="prettyprint">' + body + '</pre>')}} />
      </div>;
    }
    return (
      <Expando className="code"
               setScroll={this.props._setScroll}>
        <div className="msg-line">
          <div className="pre-wrap" dangerouslySetInnerHTML={{__html: prettyPrintOne('<pre class="prettyprint">' + body + '</pre>')}} />
        </div>
      </Expando>
    );
  },

  _replaceImageWithRetina: function (node) {
    var emotes = $(node).find('img.remoticon'),
        resolution = Math.ceil(window.devicePixelRatio);
    for (var i = 0; i < emotes.length; i++) {
      if (!~emotes[i].getAttribute('src').indexOf('@' + resolution + 'x')) this._doReplaceImage(emotes[i], resolution);
      if (emotes[i].getAttribute('src').match(/\/img\/emoticons\/scumbag/)
        && emotes[i + 1]
        && emotes[i].nextSibling === emotes[i + 1]) {
        this._scumbagify(emotes[i], emotes[i + 1]);
      }
    }
  },

  _doReplaceImage: function (img, resolution) {
    var src = img.getAttribute('src'),
        ext = src.split('.').pop();
    if (resolution && resolution > 1) {
      var image = new Image();
      image.onload = function () {
        img.src = src.replace('.' + ext, '@' + resolution + 'x.' + ext);
      };
      image.src = src.replace('.' + ext, '@' + resolution + 'x.' + ext);
    }
  },

  _scumbagify: function (hat, scumbag) {
    $(scumbag).on('load', function () {
      var width = $(scumbag).width();
      hat.parentNode.style.position = 'relative';
      hat.style.position = 'absolute';
      hat.style.top = scumbag.offsetTop - 3 + 'px';
      hat.style.left = scumbag.offsetLeft + width / 2 - 10 + 'px';
    })
  }

});



