var PresenceIcon = require("../common/icon/presence-icon");

module.exports = React.createClass({

  displayName: "LobbyItem",

  render: function(){

    var type = /@conf/.test(this.props.item.jid) ? 'groupchat' : 'chat',

    icon = type == 'groupchat' ?
      <span className={"aui-icon hipchat-icon-small icon-" + this.props.item.privacy}></span> :
      <PresenceIcon presence={this.props.item.presence? this.props.item.presence.show : 'unknown'} />;

    return (
      <tr>
        <td className="hc-lobby-list-icon">{icon}</td>
        <td className="hc-lobby-list-name">
          <a onClick={this.props.onChatOpen.bind(null, { jid: this.props.item.jid, name: this.props.item.name, type: type })}>
            {this.props.item.name}
          </a>
        </td>
        <td className="hc-lobby-list-actions">
      {/*
          <button className="aui-button aui-button-subtle">
            <span className="aui-icon aui-icon-small aui-iconfont-more">More </span>
          </button>
      */}
        </td>
      </tr>
    );
  }
});



