var UserStatus = require("./user_status");
var AppActions = require("../../actions/app_actions");
var RoomNavActions = require("../../actions/room_nav_actions");
var AppHeaderStore = require("../../stores/app_header_store");
var AppDispatcher = require('../../dispatchers/app_dispatcher');
var NotificationBanner = require('./notification_banner');
var InlineDialogActions = require('../../actions/inline_dialog_actions');

module.exports = React.createClass({

  displayName: "AppHeader",

  getInitialState: function() {
    return AppHeaderStore.getAll();
  },

  componentDidMount: function() {
    AppHeaderStore.on("change", this._onChange);
  },

  componentWillUnmount: function() {
    AppHeaderStore.off("change", this._onChange);
  },

  _onLogoClick: function() {
    AppActions.logoClicked();
  },

  _onNewChatClick: function(evt) {
    RoomNavActions.openLobby();
    evt.preventDefault();
  },

  _getUserStatus: function() {
    if (this.state.ready) {
      return <UserStatus
                search_text={this.state.search_text}
                focus_search={this.state.focus_search}
                active_chat={this.state.active_chat}/>;
    }
  },

  _getNewChatButton: function () {
    if (this.state.ready) {
      return (
        <ul className="aui-nav">
          <li>
            <a onClick={this._onNewChatClick} className="aui-button aui-button-primary" title="New chat">New chat</a>
          </li>
        </ul>
      );
    }
  },

  _getInviteUserButton: function () {
    if (this.state.ready) {
      return (
        <ul className="aui-nav">
          <li>
            <a onClick={this._onInviteUserClick} className="aui-button aui-button-link hc-invite-users aui-inline-dialog-trigger" title="Invite user">Invite your team</a>
          </li>
        </ul>
      );
    }
  },

  _onInviteUserClick: function(evt) {
    InlineDialogActions.showInviteNewUsersForm({button: evt.target});
  },

  _onChange: function() {
    this.setState(this.getInitialState());
  },

  render: function () {

    var userStatus = this._getUserStatus();
    var newChatButton = this._getNewChatButton();
    var inviteUsersButton = this._getInviteUserButton();

    var banner;
    if (this.state.notification_supported && !this.state.notification_dismissed_forever && !this.state.notification_permission) {
      banner = <NotificationBanner notification_supported={this.state.notification_supported}
        notification_dismissed={this.state.notification_dismissed}
        notification_preference={this.state.notification_preference}
        notification_dismissed_forever={this.state.notification_dismissed_forever}
        notification_permission={this.state.notification_permission}/>;
    }

    return (

      <header id="header" role="banner">
        {banner}
        <nav className="aui-header aui-dropdown2-trigger-group" role="navigation" data-aui-responsive="true">
          <div className="aui-header-inner">
            <div className="aui-header-primary">
              <h1 id="logo" className="aui-header-logo aui-header-logo-hc">
                <a href="/" onClick={this._onLogoClick}>
                  <span className="aui-header-logo-device">HipChat</span>
                </a>
              </h1>
              {newChatButton}
              {inviteUsersButton}
            </div>
            {userStatus}
          </div>
        </nav>
      </header>

    );
  }
});


