var ChatPanelStrings = require('../../strings/chat_panel_strings');
var AppDispatcher = require("../../dispatchers/app_dispatcher");

module.exports = React.createClass({

  displayName: "FailedMessage",

  _resendMessage: function () {
    AppDispatcher.dispatch("resend-message", this.props.msg);
  },

  render: function () {
    return (
      <div className="msg-fail-container">
        {this.props.children}
        <div className="msg-fail">
          <span className="aui-icon aui-icon-small aui-iconfont-error"></span>
          <span className="msg-fail-retry">
          {ChatPanelStrings.failed_message} &middot; <button className="aui-button aui-button-link" onClick={this._resendMessage}>{ChatPanelStrings.failed_message_retry}</button>
          </span>
        </div>
      </div>
    )
  }
});



