var strings = require('../../strings/empty_state_strings'),
    DialogActions = require("../../actions/dialog_actions"),
    InlineDialogActions = require("../../actions/inline_dialog_actions");

module.exports = React.createClass({

  displayName: "RightSideBarRosterEmptyState",

  _openInviteUsers: function(e) {
    e.preventDefault();
    DialogActions.showInviteUsersDialog();
  },

  _openInviteTeam: function(e) {
    e.preventDefault();
    e.stopPropagation();
    InlineDialogActions.showInviteNewUsersForm();
  },

  _getInviteMessage: function () {
    if (this.props.room_privacy === "private" && !this.props.isAdmin) {
      return false;
    } else {
      var inviteAction = this._openInviteUsers,
          inviteCTA = strings.invite_someone;

      if (_.size(this.props.users) === 1) {
        inviteAction = this._openInviteTeam;
        inviteCTA = strings.invite_teammates;
      }

      return (
        <div className="hc-tab-es-msg">
          <div>{strings.chat_empty_sub}</div>
          <a className="hc-tab-es-invite" onClick={inviteAction}>{inviteCTA}</a>
        </div>
      );
    }
  },

  render: function(){
    var inviteMessage = this._getInviteMessage();
    return (
      <div className="hc-tab-es">
        <div className="hc-tab-es-img roster"></div>
        <div className="hc-tab-es-title">{strings.chat_empty}</div>
        {inviteMessage}
      </div>
    );
  }
});


