var AppDispatcher = require('../../dispatchers/app_dispatcher'),
    AuiInputFieldGroup = require("../common/aui/form/aui_input_fieldgroup"),
    AuiTextAreaFieldGroup = require("../common/aui/form/aui_textarea_fieldgroup"),
    RoomPrivacyRadios = require("../../components/forms/room_privacy_radios"),
    FlagActions = require("../../actions/flag_actions"),
    DialogActions = require("../../actions/dialog_actions"),
    FormActions = require("../../actions/form_actions"),
    FormsStore = require("../../stores/forms_store"),
    Utils = require("../../helpers/utils"),
    strings = require('../../strings/forms_strings'),
    FormErrors = require("../../strings/form_error_strings");

module.exports = React.createClass({

  displayName: "CreateRoomForm",

  componentDidMount: function() {
    FormsStore.on('change:allRooms', this._onChange);
    this._focusInput();
  },

  componentWillUnmount: function() {
    FormsStore.off('change:allRooms', this._onChange);
  },

  componentDidUpdate: function() {
    if (this.state.errors.name) {
      this.refs.nameField.focus();
    }
  },

  getInitialState: function () {
    return this._getState();
  },

  _onChange: function(){
    this.setState(this._getState());
  },

  _getState: function(){
    return {
      allRooms: FormsStore.get("allRooms"),
      errors: {},
      room_name: null
    };
  },

  _onSubmit: function (e) {
    e.preventDefault();
    var form_inputs = $(this.refs.form.getDOMNode()).serializeArray(),
        submit_data = {
          name: form_inputs[0].value.trim(),
          topic: (form_inputs[1]) ? form_inputs[1].value.trim() : "",
          privacy: (form_inputs[2]) ? form_inputs[2].value : "public"
        };

    var errors = this._validate(submit_data.name);
    if (errors) {
      this.setState({
        errors: errors
      });
    } else {
      this.setState({
        room_name: submit_data.name
      });
      DialogActions.startBtnLoading();
      FormActions.createRoom(submit_data, this._handleResponse);
    }
  },

  _validate: function (name) {
    if (!name) {
      return {
        name: FormErrors.create_room_form.no_room_name
      };
    } else if (name.length > 50) {
      return {
        name: FormErrors.create_room_form.name_too_long
      }
    } else if (_.find(this.state.allRooms, function (room) { return room.name && (room.name.toLowerCase() === name.toLowerCase()) })) {
      return {
        name: FormErrors.create_room_form.room_already_exists
      };
    }
  },

  _handleResponse: function (data) {
    if (data.error) {
      var error_msg = (Utils.dot(data, "error.text.__text")) ? data.error.text.__text : strings.fail.create_room;
      this._throwFlagError(error_msg);
    } else {
      var name = this.state.room_name || "Room";
      var success_msg = strings.success.room_created(name);
      this._throwFlagSuccess(success_msg);
    }
    this._closeDialog();
  },

  _throwFlagSuccess: function (msg) {
    FlagActions.showFlag({
      type: "success",
      body: msg,
      close: "auto"
    });
  },

  _throwFlagError: function (error) {
    FlagActions.showFlag({
      type: "error",
      body: this._flagBody(error),
      close: "manual"
    });
  },

  _flagBody: function (error_msg) {
    return (
      <div>
        <p className="hc-message-body">{error_msg}</p>
        <ul className="aui-nav-actions-list">
          <li><a onClick={this._flagActionClick} href="#">{strings.button.try_again}</a></li>
        </ul>
      </div>
    );
  },

  _flagActionClick: function (e) {
    e.preventDefault();
    var flag_index = $(e.target).closest(".flag").data('flag-index');
    FlagActions.removeFlag(flag_index);
    this._showDialog();
  },

  _showDialog: function () {
    DialogActions.showCreateRoomDialog();
  },

  _closeDialog: function () {
    DialogActions.closeDialog();
  },

  _focusInput: function () {
    this.refs.form.getDOMNode().querySelector("#create-room-name").focus();
  },

  render: function () {

    var errors = this.state.errors;

    return (
      <form id="create-room-form" ref="form" className="aui" onSubmit={this._onSubmit} >
        <AuiInputFieldGroup disabled={this.props.loading} ref="nameField" id="create-room-name" label={strings.label.room_name} maxLength="50" description={strings.description.create_room_description} error={errors.name} />
        <AuiInputFieldGroup disabled={this.props.loading} id="create-room-topic" label={strings.label.room_topic} />
        <RoomPrivacyRadios disabled={this.props.loading} defaultChecked="public"/>
      </form>
    );
  }
});


