module.exports = React.createClass({

  displayName: "InlineDialog",

  propTypes: {
    onShow: React.PropTypes.func
  },

  componentDidMount: function() {
    this.props.onShow();
  },

  getDefaultProps: function() {
    return {
      dialogId: "dialog",
      onShow: _.noop
    }
  },

  render: function() {
    return (
      <div className="aui-inline-dialog" aria-hidden="false" id={this.props.dialogId}>
        <div className="aui-inline-dialog-contents">
          {this.props.children}
          <div className="aui-inline-dialog-arrow arrow aui-css-arrow"></div>
        </div>
      </div>
    );
  }
});


