/**
 * Room Dropdown Actions
 */

var AppDispatcher = require('../dispatchers/app_dispatcher');
var AppStore = require('../stores/application_store');

module.exports = {

  openIntegrations: function (room_id) {
    var current_user = AppStore.get("current_user"),
        url = "/rooms/addons/" + room_id + "?required_email=" + encodeURIComponent(current_user.email);
    window.open(url, "_blank");
  },

  editTopic: function () {
    AppDispatcher.dispatch('edit-topic');
  },

  deleteRoom: function (data, cb) {
    AppDispatcher.dispatch('delete-room', {
      jid: data.jid,
      reason: "Admin is deleting the room"
    }, cb);
  },

  archiveRoom: function (data, cb) {
    AppDispatcher.dispatch('archive-room', {
      jid: data.jid,
      id: data.id
    }, cb);
  },

  unarchiveRoom: function (data, cb) {
    AppDispatcher.dispatch('unarchive-room', {
      jid: data.jid,
      id: data.id
    }, cb);
  },

  enableGuestAccess: function (data) {
    AppDispatcher.dispatch('set-guest-access', {
      jid: data.jid,
      enable: true
    });
  },

  disableGuestAccess: function (data) {
    AppDispatcher.dispatch('set-guest-access', {
      jid: data.jid,
      enable: false
    });
  }

};


