var PersonStatus = require("./person_status"),
    Utils = require("../../helpers/utils"),
    PresenceIcon = require("../common/icon/presence-icon"),
    strings = require('../../strings/tooltip_strings');

module.exports = React.createClass({

  displayName: "RightSideBarPerson",

  getDefaultProps: function() {
    return {
      onPersonClick: _.noop
    }
  },

  componentDidMount: function () {
    this._createTooltip();
  },

  componentWillUnmount: function() {
    this._destroyTooltip();
  },

  _getStatus: function () {
    var status;
    if (this.props.user.presence.show === "away") {
      status = <PersonStatus status={this.props.user.presence.status} idleTime={this.props.user.presence.idleTime} />;
    } else if (this.props.user.presence.status) {
      status = <PersonStatus status={this.props.user.presence.status} />;
    }
    return status;
  },

  _onClick: function() {
    this.props.onPersonClick(this.props.user.jid);
  },

  render: function(){
    var status = this._getStatus(),
        link_classes,
        cx = React.addons.classSet;

    link_classes = cx({
      'hc-roster-link': true,
      'hc-roster-admin': this.props.isAdmin
    });

    var rosterItemClasses = cx({
      'hc-roster-item': true,
      'aui-nav-selected': this.props.active
    });

    return (
      <li className={rosterItemClasses} onClick={this._onClick} ref="person">
        <div className="aui-nav-item">
          <PresenceIcon presence={this.props.user.presence.show} active={true} />
          <a className={link_classes} onDoubleClick={this.props.onChatOpen.bind(null, { jid: this.props.user.jid, name: this.props.user.name })}>{this.props.user.name}</a>
          {status}
        </div>
      </li>
    )
  },

  _createTooltip() {
    AJS.$(this.refs.person.getDOMNode()).tooltip({
      title: () => {
        var title = `${this.props.user.name}\n` +
          `@${this.props.user.mention_name}\n` +
          `${strings.status} ${Utils.user.get_user_status(this.props.user.presence.show)}`;
        if (this.props.isAdmin) {
          title += '\n' + strings.admin;
        }
        if (this.props.user.presence.show === "dnd" && this.props.user.presence.status) {
          title += `\n${strings.message} ${this.props.user.presence.status}`;
        }
        return title;
      }
    });
  },

  _destroyTooltip() {
    AJS.$(this.refs.person.getDOMNode()).off();
  }
});



