module.exports = React.createClass({

  displayName: "NotificationMessageType",

  shouldComponentUpdate: function(nextProps, nextState) {
    return !_.isEqual(this.props, nextProps);
  },

  render: function () {
    return (this.props.msg.format == 'html' ?
      <div className="notification msg-line" data-mid={this.props.msg.mid} dangerouslySetInnerHTML={{__html: this.props.msg.body}} />
      :
      <div className="notification msg-line" data-mid={this.props.msg.mid} dangerouslySetInnerHTML={{__html: this.props.msg.rendered_body}} />);
  }

});


