var Utils = require('../helpers/utils');
var Store = require("../../lib/core/store");
var AppDispatcher = require("../dispatchers/app_dispatcher");

class CurrentUserStore extends Store {

  constructor() {
    this.data = {
      user: {},
      presence: {
        show: 'chat'
      }
    };

    this.registerListeners();
  }

  registerListeners() {
    AppDispatcher.register({
      'updated:current_user': (user) => {
        this.handleCurrentUserUpdate(user);
      },
      'updated:roster': (roster) => {
        this.data.presence = roster[this.data.user.user_jid].presence;
        this.handleCurrentUserUpdate(this.data.user);
      },
      'update-presence': (presence) => {
        this.data.presence = presence;
        this.handleCurrentUserUpdate(this.data.user);
      }
    });
  }

  handleCurrentUserUpdate(user) {
    this.set('user', _.assign(this.data.user, user));
  }

}

module.exports = new CurrentUserStore();


